@import 'src/sass/sizes';
@import 'src/sass/colors';

$program-collection-height: 440px;

.container {
  @include full-screen;
  background-color: $black;

  &.blurred {
    filter: blur(5px);
  }

  .currentClass {
    height: $screen-height - $program-collection-height;
    background-color: $black;
  }

  .programCollection {
    height: $program-collection-height;
  }
}
