@import 'src/sass/colors';

.gradient {
  position: absolute;
  height: 60px;
  width: 100%;
  left: 0px;
  bottom: 0px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.85) 30%, rgba(0, 0, 0, 0) 100%);
}

.avatar {
  width: 36px;
  height: 36px;
  font-size: 16px;
  font-weight: 100;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 -4px;
  border: solid 2px $black;
}

.lightText {
  color: $white;
}

.image {
  position: relative;
}
