@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.exerciseHistoryPage {
  margin: 120px 30px;

  .headerTitle {
    display: flex;
    margin-left: 15px;
    margin-bottom: 50px;

    .image {
      background-color: #c8d8eb;
      border-radius: 50%;
      width: 65px;
      height: 65px;
      margin-right: 15px;
      text-align: center;

      img {
        object-fit: contain;
        width: 60px;
        height: 60px;
      }
    }

    .name {
      font-size: 40px;
      color: $dark_blue;
      font-family: $moderat;
      font-weight: 500;
    }
  }

  .historyCollapse {
    border-radius: 10px;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 15px;
    color: $dark;
    font-weight: 500;
    font-size: 25px;
    padding: 40px;

    span {
      margin-left: 20px;

      svg {
        transform: scale(1.5);
      }
    }
  }

  .loader {
    text-align: center;
    width: 100%;

    font-size: 25px;
    color: $dark_blue;

    img {
      width: 40px;
    }
  }
}
