@import 'src/sass/colors';
@import 'src/sass/fonts';

.wrapper {
  box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.35);
  background-color: linear-gradient(115deg, #f6fafe, #d9e3f0);
  z-index: 1;
  color: $brand-blue;

  &.meldWithNavBar {
    margin-top: -104px;
  }

  .content {
    padding: 0px 32px 13px 32px;
  }
}

.backButton {
  padding-left: 0px;
}

.nav {
  flex-direction: row-reverse;
}

.title {
  margin-top: 36px;
  font-size: 40px;
  font-weight: 300;
  text-transform: uppercase;
  display: inline-block;

  img {
    margin-bottom: 10px;
  }
}

.description {
  margin-top: 16px;
  font-size: 22px;
  font-weight: 500;

  span {
    font-weight: 400;
  }
}

.progressbar {
  margin-top: 27px;

  > div:last-child {
    margin-top: 9px;
    font-size: 25px;
    text-align: center;
    color: #434c5b;
  }
}

.divider {
  border-left: 2px solid $dark;
  height: 27px;
  margin: 0 30px;
}

.stats {
  display: flex;
  margin-top: 50px;

  .stat {
    width: 314px;
    font-size: 18px;
    text-transform: uppercase;
    color: $dark;

    h1 {
      font-size: 100px;
      font-family: $heroic;
      font-weight: 300;
      font-stretch: condensed;

      small {
        font-size: 48px;
        text-transform: lowercase;
      }
    }
  }
}
