.container {
  display: flex;
  margin-right: 16px;
  height: 40px;
  min-width: 40px;
  max-width: 260px;
  border: 2px solid rgba(153, 159, 172, 0.2);
  border-radius: 40px;
  color: white;

  &.isActive {
    border: 2px solid white;
    background: white;
    color: black;
  }

  .icon {
    padding: 4px 0px 0px 8px;

    &.withTitle {
      padding: 4px 0px 0px 14px;
    }
  }

  .text {
    margin: 4px 16px 6px 8px;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    white-space: nowrap;
    overflow: hidden;
  }
}
