.filter-panel {
  .classFilterItem {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 79.49px;
    align-items: center;
    color: #4f5888;
    cursor: pointer;

    .iconWrapper {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      svg {
        fill: #4f5888;
        height: 55px;
        max-width: 80px;
        margin-bottom: 13px;
      }
    }
  }

  .selected {
    color: #f9423a;
    border-color: #f9423a;
    .iconWrapper {
      svg {
        fill: #f9423a;
      }
    }
  }
}
