@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.status {
  display: flex;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 300;
  line-height: 28px;
  align-items: center;

  img {
    margin-right: 12px;
  }
  &.completed {
    color: $green;
  }
  &.missed {
    color: $label-gray;
  }
}
