@import '../../sass/colors';

.wrapper.inline {
  display: inline-block;
}

.innerWrapper {
  display: flex;
}

.weights {
  position: relative;
  width: 46px;
  height: 60px;
  margin: 0 25px;

  &.medium {
    width: 32px;
    height: 32px;
    margin: 0 10px;
  }

  &.small {
    width: 23px;
    height: 23px;
    margin: 0 7px;
  }

  .weight {
    display: inline-block;
    width: 46px;
    height: 46px;
    border-radius: 100%;
    box-shadow: 0px 0px 0px 2px black;
    border: 12px solid white;
    position: relative;

    &.medium {
      border: 7px solid white;
      width: 32px;
      height: 32px;
    }

    &.small {
      width: 23px;
      height: 23px;
      border: 4px solid white;
    }

    & + span {
      position: absolute;
      background: #fff;
      color: #2a2d3a;
      border: solid 2px #2a2d3a;
      border-radius: 10px;
      min-width: 2.2em;
      text-align: center;
      left: 66%;
      top: -6px;
      font-size: 20px;
    }

    & + span.medium {
      font-size: 12px;
      top: -4px;
      border-radius: 8px;
    }

    & + span.small {
      font-size: 10px;
      top: -12px;
      border-radius: 8px;
    }

    &.blue {
      border-color: $pivot-light-blue;
      box-shadow: 0px 0px 0px 3px #6b8098, inset 0px 0px 0px 3px #6b8098;
    }

    &.navy {
      border-color: $pivot-navy;
      box-shadow: 0px 0px 0px 3px #1a1d46, inset 0px 0px 0px 3px #1a1d46;
    }

    &.red {
      border-color: $pivot-red;
      box-shadow: 0px 0px 0px 3px #ab2620, inset 0px 0px 0px 3px #ab2620;
    }

    &.yellow {
      border-color: $pivot-yellow;
      box-shadow: 0px 0px 0px 3px #bbb734, inset 0px 0px 0px 3px #bbb734;
    }

    &.gray {
      border-color: #bababa;
      box-shadow: 0px 0px 0px 3px #959595, inset 0px 0px 0px 3px #959595;
    }

    &.darkGray {
      border-color: #575757;
      box-shadow: 0px 0px 0px 3px #3f3f3f, inset 0px 0px 0px 3px #3f3f3f;
    }
  }
}

.label {
  color: #434c5b;
  font-weight: 500;
  display: block;
  width: 100%;
}

@keyframes rest-icon-scale1 {
  0%,
  100% {
    transform: scale(0);
  }
}

@keyframes rest-icon-scale2 {
  0% {
    transform: scale(0);
  }
}
