@mixin centered-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  height: 1920px;
  background-color: black;
  position: relative;

  img {
    top: 0;
    position: absolute;
    width: 100%;
  }

  .content {
    position: relative;
    z-index: 1;
    height: 1600px;
    width: 100%;

    @include centered-column();
    justify-content: flex-end;

    text-align: center;
    color: white;

    .classInformation {
      width: 900px;
      height: 450px;

      @include centered-column();
    }

    .title {
      h1 {
        font-size: 52px;
        font-weight: 500;
      }

      margin-bottom: 16px;
    }

    .body {
      width: 756px;
      text-align: center;
      font-size: 22px;
      font-weight: 400;

      margin-bottom: 56px;
    }
  }
}
