@import '../../sass/fonts';
@import '../../sass/colors';

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: $light-bg;

  &.withBottomNav {
    height: calc(100vh - 104px);
    padding-bottom: 96px;
  }
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.classes {
  padding: 30px;
  overflow-y: auto;

  > div + div {
    margin-top: 15px;
  }
}

.scrollTarget {
  scroll-margin-top: 64px;
}
