.overlay {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  background: rgba(0, 0, 0, 0.7);
}

.topBar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  display: flex;

  background: #434c5b;
  color: white;
  padding: 16px;
}

.leftContent {
  flex: 1;
  display: flex;
}

.exitButton {
  border: none;
  outline: none;
  background: transparent;
  color: white;

  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  text-transform: uppercase;
}

.rightContent {
  display: flex;
  align-items: center;

  text-align: right;
}

.classDetails {
  margin-right: 24px;
}

.classTitle {
  margin-bottom: 0;
  font-size: 26px;
  line-height: 32px;
  font-weight: 400;
}

.instructorName {
  margin-bottom: 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  text-transform: uppercase;
}

.instructorImageContainer {
  overflow: hidden;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background: white;
}

.instructorImage {
  width: 100%;
}

.pauseIcon {
  color: white;
}
