.audioQaComponent {
  & > * {
    margin-bottom: 1rem;
  }

  & > *:last-child {
    margin-bottom: unset;
  }

  .volumeContainer {
    background-color: gray;
    width: fit-content;
    border-radius: 29px;
  }

  .questionaire {
    & > * {
      margin-bottom: 1rem;
    }

    & > *:last-child {
      margin-bottom: unset;
    }

    .responses {
      & > * {
        margin-right: 1rem;
      }

      & > *:last-child {
        margin-bottom: unset;
      }
    }
  }
}
