@import 'src/sass/fonts';
@import 'src/sass/colors';

.page {
  font-family: $moderat;
  font-style: normal;
  text-align: center;
  font-weight: normal;
  color: $white;
  width: 1080px;
  height: 1920px;
  background: $black;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .header {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    text-transform: uppercase;
    position: absolute;
    top: 105px;
  }

  .title {
    font-weight: bold;
    font-size: 60px;
    line-height: 70px;
    margin-top: 208px;
    margin-bottom: 24px;
    width: 796px;
    white-space: pre-wrap;
    font-weight: 400;
  }

  .subtitle {
    font-size: 26px;
    line-height: 32px;
    width: 900px;
    opacity: 1;
    white-space: pre-wrap;
  }

  .topMedia {
    margin-top: 176px;
    height: 700px;
    width: 100%;
    object-fit: cover;
  }

  .bottomMedia {
    margin-top: 29px;
  }

  .error {
    font-size: 22px;
    margin-top: 36px;
  }
}
