@import 'src/sass/fonts';
@import 'src/sass/colors';

.content {
  text-align: center;
  color: #3d4756;
  font-size: 35px;
  line-height: 1.36;
  margin: 30px auto;
  padding: 0px 15px;
  font-weight: 300;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.resumableContent {
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

.title {
  @include headling-medium;
  color: #434c5b;
  margin-bottom: 16px;
  text-align: center;
}

.description {
  @include body-sm-regular;
  width: 640px;
  color: #434c5b;
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.resumeButton {
  text-transform: uppercase;
  padding: 0;
  margin-top: -1rem;
}
