@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .message {
    @include headling-medium;
    color: $dark_blue;
    text-align: center;
    margin-bottom: 32px;
  }

  .errorMessage {
    @extend .message;
    margin-bottom: 16px;
  }

  .errorDetails {
    @include body-regular;
    color: $dark_blue;
    text-align: center;
    margin: 0 -16px 32px -16px;
  }

  .invalidPassword {
    @include body-l-regular;
    color: $primary;
    margin-top: -8px;
    margin-bottom: 40px;
  }

  .spinner {
    width: 60px;
    height: 60px;
    margin-bottom: 32px;
  }

  .icon {
    width: 60px;
    height: 60px;
    margin-bottom: 32px;
  }

  .macNote {
    margin-top: 40px;
  }
}
