@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.navBar {
  display: flex;
  flex-direction: row;
  padding-left: 30px;
  height: 80px;
  align-items: center;
}

.highContrastNavItem {
  color: $dark;
  margin-right: 16px;
  border: 2px solid $dark !important;
  @include body-sm-uppercase;
  line-height: 44px;
  border-radius: 22px;
  padding-left: 22px;
  padding-right: 22px;
}

.highContrastSelected {
  color: $white;
  background-color: $dark;
}
