@import 'src/sass/colors.scss';

$tab-bar-height: 87px;

.navMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: $tab-bar-height;
  background-color: $white;
  color: $black;
  box-shadow: 0px 7px 13px 0px #3d457b59;
  text-transform: uppercase;

  &.darkTheme {
    background-color: $black;
    color: $white;
    box-shadow: none;

    .avatar {
      background-color: $white;
    }
  }

  svg {
    margin-top: 14px;
    margin-bottom: 6px;
  }

  .avatar {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    margin-top: 14px;
    margin-bottom: 6px;
    background-color: #9da1bc;
  }
}
