.container {
  z-index: 9999;
  position: fixed;
  bottom: 40px;
  left: 35px;

  svg {
    height: 63px;
  }
}

.trigger {
  outline: none;
  border: none;
  background: none;

  border-radius: 50%;
  padding: 0;
  box-shadow: 0px 7px 13px rgba(61, 69, 123, 0.348392);

  transform: scale(1);
  transition: transform 0.3s;

  &:active {
    transform: scale(0.9);
  }

  &:focus {
    outline: none;
  }
}

.menu {
  position: absolute;
  bottom: 120%;
  left: 10%;
  padding: 24px 16px 16px 16px;

  box-shadow: 0px 7px 13px rgba(61, 69, 123, 0.348392);
  border-radius: 4px;
  background: #f0f4f7;
  color: #434c5b;
}
