@import '../../sass/fonts.scss';
@import '../../sass/colors.scss';

.profile-page {
  &.wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  > .content {
    flex: 1;
    padding: 30px 0;
    background-image: linear-gradient(158deg, #f6fafe, #d9e3f0);
    overflow-y: auto;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 22px;
    font-weight: 300;
    color: #3d457b;
    text-transform: uppercase;
    padding: 0 30px;

    &.topHeader {
      justify-content: flex-end;
    }

    .settings {
      display: flex;
      align-items: center;
      cursor: pointer;
    }

    .manageProfiles {
      display: flex;
      align-items: center;
      cursor: pointer;

      svg {
        margin-left: 15px;
      }
    }
  }

  .profile-page__title {
    margin-top: 30px;
    margin-bottom: 22px;
    font-size: 30px;
    font-weight: 300;
    color: #434c5b;
    text-transform: uppercase;
    padding-left: 30px;
  }

  .fixedHeader {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 13px 30px;
    width: 100%;
    box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.15);
    background-image: linear-gradient(95deg, #707bb2, #3d457b);
    z-index: 1;

    .avatarIcon {
      width: 63px;
      height: 63px;
      font-size: 32px;
      font-weight: 100;
    }

    .status {
      position: absolute;
      bottom: 5px;
      right: 5px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #07d824;
    }

    > label {
      margin-left: 13px;
      font-size: 26px;
      color: #ffffff;
      max-width: 250px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .followersAndGroupsContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 40px 0;

    > div {
      width: 324px;
    }
  }

  .followersContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 324px;

    > div {
      margin-bottom: 10px;
    }

    > div:last-child {
      margin-bottom: unset;
    }
  }

  .avatarContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 38px;

    .avatar1 {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      color: #3d4756;
      font-size: 40px;

      .avatarIcon {
        width: 169px;
        height: 169px;
        font-size: 85px;
        font-weight: 100;
      }

      > label {
        padding: 0px 30px;
        margin-top: 22px;
        max-width: 500px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      .status {
        position: absolute;
        bottom: 14px;
        right: 14px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: #07d824;
      }
    }

    .avatar2 {
      position: absolute;
      top: 13px;
      right: -170px;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      color: #3d4756;
      font-size: 30px;
      opacity: 0.6;

      > label {
        margin-top: 13px;
      }
    }
  }

  .activityModule {
    margin: 30px 0 0;
  }

  .milestonesContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 40px 0;
  }

  .progressBoard {
    width: 100%;
    margin-top: 30px;
  }

  .workouts {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 -30px;
  }

  .classes {
    > label {
      display: block;
      margin-top: 20px;
      font-size: 20px;
      font-weight: 500;
      color: #434c5b;
    }

    > div.class-session-item {
      margin: 25px -30px 0;
      box-shadow: none;
      > .day {
        width: 92px;
        span {
          font-size: 80px;
          color: $dark;
          line-height: 96px;
        }
      }
      > .thumbnail {
        width: 390px;
        border-radius: 6px;
        img {
          object-fit: cover;
          height: 100%;
        }
      }
      .title0 {
        color: transparentize($color: $label-gray, $amount: 0.3);
        opacity: 1;
        span {
          color: $dark;
        }
      }
      .title {
        color: $dark-gray;
        font-size: 26px;
        line-height: 31px;
        font-weight: bold;
        margin-bottom: 15px;
      }
      .info {
        padding-left: 25px;
      }
    }
  }

  .panel {
    padding: 38px 32px;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 #c8d8eb;
    background-color: #ffffff;
    margin: 0 30px;

    hr {
      margin: 60px 0;
      border-top: solid 1px $label-gray;
    }

    h1 {
      font-size: 26px;
      line-height: 32px;
      text-transform: uppercase;
      font-weight: bold;
      color: $dark-gray;
      margin: 50px -10px 15px;
    }
  }
  .calendar-panel {
    min-height: 100vh;
    scroll-margin-top: 80px;
  }
}
