.radioInput {
  position: relative;
  margin: 0;
  min-height: 36px;
  line-height: normal;
  overflow: hidden;

  input {
    position: absolute;
    cursor: pointer;
    height: 0;
    width: 0;
    left: -30px;

    &:after {
      content: '';
      display: inline-block;
      line-height: 100%;
      margin-left: 30px;
      width: 36px;
      height: 36px;
      background: url(../../assets/icons/radio-inactive.svg) no-repeat center center;
    }

    &:checked:after {
      content: '';
      display: inline-block;
      line-height: 100%;
      margin-left: 30px;
      width: 36px;
      height: 36px;
      background: url(../../assets/icons/radio-active.svg) no-repeat center center;
    }
  }

  .label {
    margin: 3px 0 20px 68px;
    color: #3d4756;
    font-size: 25px;
    font-weight: 500;
  }
}
