.set-feedback-details {
  display: flex;
  flex-direction: column;
  text-align: left;

  &__item {
    $parent: &;
    display: flex;
    padding: 32px;
    align-items: center;
    border-radius: 10px;
    margin-top: 10px;
    background: #5f535310;
    #{$parent}--icon:after {
      background: #f9423a;
    }

    &--title {
      flex: 0 0 348px;
      max-width: 348px;
      font-size: 22px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #434c5b;
      padding: 4px 22px 4px 4px;

      .playButton {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        padding: 6px;
        overflow: hidden;
        white-space: nowrap;
        text-transform: capitalize;
        text-overflow: ellipsis;
        border: 2px solid #3d457b;
        padding: 10px 10px 10px 15px;
        text-align: center;
        cursor: pointer;

        .playIcon {
          margin: auto 12px auto 4px;
        }
      }
    }

    &--description {
      border-left: solid 1px #434c5b33;
      padding: 4px 0 4px 22px;
      flex-grow: 1;
      width: 50%;
      font-family: Moderat;
      font-size: 17px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: transparentize(#434c5b, 0.4);
    }
  }
}

.correctedLabel {
  color: #88ca3e;
  font-size: 17px;
  margin-bottom: 5px;
}
