@import 'src/sass/colors.scss';

.graph {
  padding: 30px 25px 50px 25px;
  border-radius: 10px;
  background-color: white;
  margin-bottom: 30px;

  :global {
    .axis {
      color: $dark;
      opacity: 0.5;
      font-size: 18px;
      text-anchor: start;
    }

    .area-path {
      fill: url(#area-color);
    }

    .line-path {
      fill: none;
      stroke: url(#line-color);
      stroke-width: 2;
    }

    .point {
      stroke: none;
      fill: url(#line-color);
    }

    .graph-tooltip {
      position: absolute;
      border: 2px solid #98a0bc;
      border-radius: 3px;
      text-align: center;
      padding: 10px 15px;
      z-index: 10;
      background-color: white;
      visibility: hidden;

      .text {
        opacity: 0.6;
        font-weight: 500;
        color: $dark_blue;
        font-size: 18px;
      }
    }

    .controls {
      margin: 0 50px 50px 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      align-content: center;
      font-weight: 500;
      color: #98a0bc;
      font-size: 22px;

      .switch {
        display: flex;
        align-items: center;

        .box {
          position: relative;
          display: inline-block;
          height: 50px;
          width: 110px;
          border-radius: 40px;
          border: 3px solid #98a0bc;
          margin: 0 20px;
        }

        .slider {
          position: absolute;
          height: 48px;
          width: 48px;
          top: -2px;
          left: -1px;
          right: -2px;
          background-color: #98a0bc;
          transition: 0.3s;
          border-radius: 50%;

          &.slide {
            transform: translateX(59px);
          }
        }
      }

      .types {
        text-transform: uppercase;

        .type {
          padding: 10px 30px;

          &:first-child {
            margin-right: 30px;
          }
        }

        .active {
          color: white;
          background-color: #98a0bc;
          border-radius: 30px;
        }
      }
    }

    .no-data-fallback {
      opacity: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $dark;
      font-size: 18px;
      text-align: center;
    }
  }
}
