.container {
  width: 960px;
  .fields {
    display: grid;
    grid-column-gap: 15px;
    grid-template-columns: repeat(2, 1fr);

    .split {
      display: flex;

      & > div:first-child {
        margin-right: 15px;
      }

      margin-bottom: 50px;
    }

    .dropdown {
      margin-bottom: 18px;
    }
  }
}
