@import 'src/sass/colors';

.container {
  position: relative;
  height: 361px;
  width: 258px;
  background-color: $white;
  color: $black;

  img {
    z-index: -10;
  }

  h1 {
    position: absolute;
    font-size: 26px;
    z-index: 10;
    left: 18px;
    bottom: 18px;
    margin-bottom: 0px;
  }

  .gradient {
    position: absolute;
    bottom: 0;
    height: 155px;
    width: 100%;
    background: linear-gradient(180deg, rgba(229, 228, 226, 0) 0%, rgba(228, 228, 228, 0.65) 100%);
  }
}
