.expanderWrapper {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 16px;
  color: #3d457b;

  &.expanded {
    bottom: 32px;
  }

  .expanderLink {
    padding: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: #fff;
  }

  .expander {
    margin-left: 18px;
  }
}
