@import 'src/sass/sizes';

.timerContainer {
  display: flex;
  align-items: center;
  margin-top: -10px;
  position: relative;
  width: calc(100% - #{$remaining-timer-width});
  > .info {
    position: relative;
    margin-left: -35px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;

    > div {
      position: relative;
      display: flex;
      align-items: center;

      .nameReps:first-child {
        align-self: center;
        margin-top: 0;

        &.untrackedSet {
          margin-top: 50px;
        }
      }
    }
  }

  .nameReps {
    text-transform: uppercase;
    margin-left: 34px;
    margin-top: 50px;
    align-self: flex-start;
  }
}
