.nameReps {
  display: inline-flex;
  flex-direction: column;
  color: #fff;
  max-width: 680px;

  & > * {
    margin-bottom: 18px;
  }

  .label {
    font-size: 22px;
  }

  .name {
    font-size: 40px;
    flex-wrap: wrap;
  }

  .reps {
    font-size: 30px;
    font-weight: 300;
    flex-wrap: wrap;
  }

  .info-wrapper {
    display: flex;
    align-items: center;
  }

  &.wrap .name,
  &.wrap .label {
    max-width: 300px;
  }

  &.wrap .info-wrapper .reps {
    max-width: 100%;
  }

  &.wrap {
    .name,
    .info-wrapper {
      flex-wrap: wrap;
    }
  }

  .panelAni {
    &-enter {
      opacity: 0.01;
      &.panelAni-enter-active {
        transform: translateY(-30%);
        opacity: 1;
        transition: all 300ms ease-in;
      }
    }
    &-leave {
      opacity: 0.9;
      &.panelAni-leave-active {
        transform: translateY(-30%);
        opacity: 0;
        transition: all 300ms ease-in;
      }
    }
  }
}
