@import 'src/sass/colors.scss';

.dateContainer {
  background-color: rgba(158, 161, 190, 0.25);
  padding: 28px 40px;
  font-size: 25px;
  color: $dark_blue;

  &:first-child {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
}

.workoutData {
  background-color: white;
  padding: 32px 40px;
  display: grid;
  grid-template-columns: 350px 200px 300px;

  &:last-child {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }

  .classSessionInfo {
    img {
      width: 203px;
      height: 134px;
      object-fit: contain;
      border-radius: 8px;
      margin-top: 12px;
    }

    .title {
      font-size: 22px;
      font-weight: 300;
      color: $dark_blue;
      margin-top: 17px;
    }
  }

  .stats {
    text-align: center;

    .num {
      font-size: 22px;
      font-weight: 300;
      color: $dark_blue;
      margin-bottom: 32px;

      .weight {
        display: inline-block;
        min-width: 100px;
        text-align: right;
      }

      .no-plates {
        text-align: center;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .typeHeader {
      font-size: 18px;
      font-weight: 500;
      color: rgba(67, 76, 91, 0.5);
      text-transform: uppercase;
      margin-bottom: 35px;
    }
  }

  .weightPlates {
    margin-left: 10px;
  }
}
