.container {
  position: relative;
  width: 480px;
  height: 210px;
}

.heading {
  display: flex;
}

.title {
  font-size: 28px;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: white;
}

.subtitle {
  margin: 0 32px 0 auto;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  color: white;
  opacity: 0.6;
}

.chartContainer {
  position: relative;
  margin: 16px 16px 0 0px;

  .chart {
    position: absolute;
    height: 162px;
    width: 432px;
    top: 0px;
    left: 8px;
  }

  .zone {
    position: absolute;
    top: 56px;
    left: 0;
    height: 54px;
    width: 448px;
    background: linear-gradient(0, white 0%, rgba(255, 255, 255, 0.33) 100%);
    opacity: 0.5;
  }
}
