.fontWeightNormal {
  font-weight: 400;
}

.transparent {
  opacity: 0;
}

.hasWorkouts {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: var(--color-red);
  }
}

.selected {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: var(--color-red);
  }
}
