@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  font: $moderat;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
  height: 100vh;
  background-color: $black;
  overflow: hidden;
}

.avatar {
  border-radius: 50%;
  opacity: 1;
  margin-top: 38px;
  margin-bottom: 19px;
  width: 182px;
  height: 182px;
  background-size: cover;
  background-position: center center;
  font-size: 91px;
  font-weight: 100;
}

.users {
  display: flex;
  overflow: auto;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  z-index: 1;
  width: 680px;

  & .user {
    display: flex;
    flex-direction: column;
    width: 255px;
    min-height: 325px;
    align-items: center;
    font-size: 30px;
    color: $white;

    .name {
      max-width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding: 0px 12px;
      text-align: center;
    }
  }

  & .add {
    display: flex;
    justify-content: center;
    width: 255px;
    min-height: 325px;
    img {
      margin: 38px auto 105px;
    }
  }

  & .disabled {
    opacity: 0.8;
    cursor: not-allowed;
    pointer-events: none;
  }
}

.loader {
  height: 35px;
  width: 35px;
}
