@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 209px;
  height: 144px;
  margin: 25px auto;

  .count {
    font-family: $heroic;
    color: $white;
    font-size: 120px;
    line-height: 141px;
    font-weight: 300;
  }

  .button {
    height: 60px;
    width: 60px;
    border-radius: 60px;
    border: 3px solid $white;
    display: flex;
    justify-content: center;
    align-items: center;
    &:focus {
      outline: none;
    }
  }

  .recommended {
    padding: 8px 12px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 8px;
    background-color: $white;
    color: $black;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 22px;
    line-height: 28px;
    position: absolute;
    bottom: -35px;
    left: 8px;
  }

  .hidden {
    visibility: hidden;
  }
}
