.container {
  display: flex;
}

.heartRateContainer {
  height: 210px;
  width: 212px;

  opacity: 1;
  transition: opacity 250ms;
}

.fadedOut {
  opacity: 0.6;
}

.title {
  margin-bottom: 32px;

  font-size: 28px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;

  color: white;
  opacity: 0.6;
}

.heartRate {
  display: flex;
  align-items: center;
  margin-bottom: 10px;

  .heartRateCount {
    margin-right: 16px;
    width: 80px;

    font-family: Heroic;
    font-size: 80px;
    font-weight: 400;
    line-height: 80px;

    color: white;
  }

  .heartRateIcon {
    color: white;
  }
}

.zoneText {
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  text-transform: uppercase;

  color: white;
  opacity: 0.6;
}

.zones {
  width: 16px;
  height: 181px;

  .zoneDefault {
    width: 16px;
    height: 33px;
    margin-bottom: 4px;
    background-color: #363636;
  }

  .zone5 {
    background-color: #e15447;
  }

  .zone4 {
    background-color: #e38d54;
  }

  .zone3 {
    background-color: #a4c373;
  }

  .zone2 {
    background-color: #65aab4;
  }

  .zone1 {
    background-color: #929090;
  }
}
