@import 'src/sass/colors.scss';

.modal {
  max-width: 818px;
  height: 721px;
  position: relative;

  .content {
    margin-top: 26px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $dark_blue;

    h1 {
      margin: 0;
    }

    .item {
      margin-top: 76px;
      text-transform: uppercase;
    }

    .buttons {
      position: absolute;
      bottom: 56px;
      left: 0;
    }
  }
}
