@import 'src/sass/colors';
@import 'src/sass/fonts';

.list {
  background: #fff;
  padding-inline-start: 0;
  margin: 0;
  position: relative;
}

.listItem {
  display: flex;
  font-family: $moderat;
  margin: 0 30px;
  list-style: none;
  border-bottom: solid #3d457b12 2px;
  align-items: center;
  color: #434c5b;
  cursor: pointer;

  &:last-child {
    border-bottom: none;
  }
}
