@import 'src/sass/fonts.scss';

.classDay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 124px;
  font-size: 24px;
  text-transform: uppercase;
  color: #4a4a4a;

  span {
    font-family: $font-numbers;
    font-size: 86px;
    line-height: 0.8em;
    font-weight: 300;
    margin-top: 8px;
  }
}

// kilo overhaul
.day {
  font-family: $font-numbers;
  font-size: 80px;
  line-height: 96px;
  font-weight: 300;
}

.upper {
  text-transform: uppercase;
}
