.circuitCard {
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  margin-bottom: 14px;
  font-family: Moderat;

  .circuitTitle {
    text-transform: capitalize;
    font-size: 35px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 110px;
    letter-spacing: normal;
    color: #3d4756;
    padding-left: 46px;
    border-bottom: 1px solid rgba(67, 76, 91, 0.2);
  }

  .circuitCardContents {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 7px 46px;

    .exercisesTitles {
      .exerciseTitle {
        text-transform: uppercase;
        font-size: 22px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #434c5b;
        margin: 25px 0px;
      }
    }
  }

  .exercisesImagesContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    position: relative;

    .imagesGroup {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 3px auto;

      &:after {
        content: ' ';
        background: #f5f5f8;
        border-radius: 8px;
        width: 100%;
        height: 67px;
        display: block;
        position: absolute;
        z-index: 1;
      }

      .image {
        height: 125px;
        margin: auto 6px;
        z-index: 2;
      }
    }
  }
}
