@import 'src/sass/colors';

.userCard {
  display: flex;
  margin-bottom: 20px;
  color: $dark_blue;

  .info {
    display: flex;
    flex-direction: column;
    margin-left: 20px;

    .name {
      font-size: 20px;
      font-weight: 500;
    }

    .gender {
      opacity: 0.7;
      font-size: 14px;
      margin-top: -5px;
      text-transform: capitalize;
    }

    .followButton {
      margin-top: 10px;
    }
  }
}

.avatarIcon {
  width: 85px;
  height: 85px;
  font-size: 30px;
}
