.shutdown {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: black;
}

.updateControls {
  position: absolute;
  top: 900px;
  left: 0px;
  width: 100%;
}

.status {
  position: absolute;
  bottom: 50px;
  left: calc(50% - 175px);
  width: 350px;
  color: white;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;

  :global(.progress) {
    height: 4px;
    margin-bottom: 12px;
  }
}
