@import 'src/sass/colors';

.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 112px;
  width: 100%;
  background-color: $black;

  section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    .logo {
      width: 132px;
      margin-left: 28px;
    }

    .countdown {
      font-size: 80px;
      line-height: 80px;
      margin-right: 40px;
      color: rgba(255, 255, 255, 0.2);

      &.inPosition {
        color: $white;
      }
    }
  }

  .progressBar {
    display: flex;
    align-items: center;
    height: 8px;
    background-color: rgba(255, 255, 255, 0.2);

    .progress {
      height: 100%;
      background-color: $hero-yellow;
      transition: width linear 1s;

      &.multipleUsers {
        background-color: $pivot-red;
      }
    }
  }
}
