@import 'src/sass/colors.scss';

.container {
  flex: 1;
  max-width: calc(100% / 3);
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 3px solid transparent;
  font-size: 16px;
  font-weight: 500;
  color: #9da1bc;
  cursor: pointer;
  text-transform: uppercase;

  &.darkTheme {
    color: $white;
  }

  &.selected {
    color: $primary;
    border-color: $primary;
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
