@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  width: 328px;
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  .imageContainer {
    width: 100%;
    height: 280px;
    border-radius: 10.5px;
    background: $white;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 14px 12px;
    position: relative;

    .time {
      position: relative;
      height: 143px;
      width: 143px;
      left: -12px;
      top: -6px;
      text-align: center;
      color: $dark;

      .timer {
        margin-left: 20px;
      }

      .clock {
        font-family: $moderat;
        font-style: normal;

        .clockLabel {
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;
          text-transform: uppercase;
          margin-bottom: 14px;
        }

        .clockTime {
          font-family: $heroic;
          font-size: 80px;
          line-height: 80px;
          font-stretch: condensed;
          font-weight: 300;
          letter-spacing: normal;
          text-align: center;
          margin-bottom: -2px;
        }

        .meridiem {
          font-weight: normal;
          font-size: 22px;
          line-height: 28px;
        }
      }

      .timerLabel {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 15px;
      }
    }

    .image img {
      position: absolute;
      width: 282px;
      height: 343px;
      left: 91px;
      top: -7px;
      z-index: 1;
      object-fit: cover;
    }

    .lower {
      display: flex;
      z-index: 2;
      padding-left: 4px;

      .registrants {
        position: absolute;
        right: 16px;
        bottom: 16px;
        background: $white;
        border: 2px solid $dark;
        box-sizing: border-box;
        border-radius: 30px;
        width: 69px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: $dark;

        .participantLogo {
          margin-right: 4px;
        }
      }
    }
  }
}

.elapsedLabel {
  text-transform: uppercase;
  white-space: nowrap;
  align-self: center;
}
