.heartRate {
  margin: 25px 25px 0;
}

.chart {
  .axis-label {
    color: var(--color-base-40);
    font-size: 17px;
    opacity: 0.5;
    text-anchor: start;
  }

  .area-path {
    fill: url(#area-color);
  }

  .line-path {
    fill: none;
    stroke: url(#line-color);
    stroke-width: 2;
  }
}
