@import 'sass/heroic.css';
@import 'sass/moderat.css';
@import 'sass/fonts.scss';
@import 'sass/colors.scss';

$theme-colors: (
  'primary': $primary,
  'secondary': $secondary,
  'dark': $dark,
  'brand-blue': $brand-blue,
);

$font-family-sans-serif: Moderat, 'Helvetica Neue', Helvetica, Arial, sans-serif !default;
$headings-font-family: inherit !default;

$body-bg: $light-bg;

@import '../node_modules/bootstrap/scss/bootstrap.scss';

.btn {
  text-transform: uppercase;
  font-weight: 500;
  border-width: 2px;
}

body {
  overflow: scroll;
  user-select: none;
}

// Hide all scrollbars in the app - scroll should be visible from context like iOS
*::-webkit-scrollbar {
  display: none;
}

.alert {
  &.alert-danger:not(.relative) {
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}

a.unstyled {
  color: inherit;
  text-decoration: none;
}

p:last-child {
  margin-bottom: 0;
}

/* A value, like rep counts or leaderboard rank e.g. 13/30 */
.value {
  sup {
    top: -0.25em; // this lines up better than Bootstrap's -0.5em in Heroic Condensed.
  }
}
