@import 'src/sass/fonts';
@import 'src/sass/colors';

.page {
  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;

  h1 {
    font-family: Moderat;
    font-weight: 500;
    font-size: 75px;
    color: $dark_blue;
    margin-top: 480px;
  }

  .message {
    @include body-l-regular;
    margin-top: 32px;
    color: $dark_blue;
  }

  .image {
    background: #f0f4f7 url(../../assets/images/first-boot-intro.jpg);
    background-blend-mode: darken;
    width: 1080px;
    height: 1254px;
    margin-top: 140px;
  }
}
