@import 'src/sass/colors';

.trackingError {
  height: 103px;
  width: 196px;
  border-radius: 6px;
  background-color: rgba(249, 66, 58, 0.5);
  color: $white;

  .text {
    text-transform: uppercase;
    padding: 10px 14px 14px 14px;
    font-weight: 500;
    font-size: 18px;
  }

  .icon {
    display: block;
    padding-top: 9px;
    padding-left: 16px;
    margin-bottom: -11px;
  }
}
