@import '../../sass/fonts';
@import '../../sass/colors';
@import '../../sass/sizes';

.videoDebugInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  color: white;
  overflow: hidden;

  .headerOld {
    position: absolute;
    top: 0;
    width: 100%;

    z-index: 100;

    padding: 30px 30px 30px 20px;
    transition: all ease-in-out 0.2s;

    &.actionsExpanded {
      top: 87px;
    }
  }

  .headerNew {
    position: absolute;
    top: 0;
    width: 100%;

    z-index: 100;

    padding: 64px 48px;
    transition: all ease-in-out 0.2s;

    &.actionsExpanded {
      top: 87px;
    }
  }

  .leaderboard {
    position: absolute;
    top: 600px;
    right: 0;
  }

  .footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    z-index: 98;
  }

  .footerGradient {
    padding: 0 48px;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
  }

  .heroMetrics {
    position: relative;
    padding-bottom: 33px;
  }

  .heroMetricsOld {
    position: relative;
    padding-bottom: 24px;
  }

  .remainingTimeOld {
    position: absolute;
    top: 75px;
    right: 42px;
  }

  .remainingTimeNew {
    position: absolute;
    top: 64px;
    right: 48px;
  }

  :global(.vjs-custom-waiting .vjs-loading-spinner) {
    display: block;
    visibility: visible;
  }

  :global(.video-js.vjs-custom-waiting .vjs-loading-spinner:before),
  :global(.video-js.vjs-custom-waiting .vjs-loading-spinner:after) {
    /* I just copied the same animation as in the default css file */
    -webkit-animation: vjs-spinner-spin 1.1s cubic-bezier(0.6, 0.2, 0, 0.8) infinite,
      vjs-spinner-fade 1.1s linear infinite;
    animation: vjs-spinner-spin 1.1s cubic-bezier(0.6, 0.2, 0, 0.8) infinite, vjs-spinner-fade 1.1s linear infinite;
  }

  .forceReload {
    animation: fadein ease-in 0.3s;
    animation-delay: 3s;
    animation-fill-mode: backwards;
  }
}

.classVideoPlayer {
  animation: fadein 3s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
