.container {
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 30px;
  padding-right: 30px;

  text-align: center;
  color: #434c5b;
}

.title {
  margin-bottom: 65px;

  font-size: 40px;
  line-height: 47px;
  font-weight: 500;
}

.detailsContainer {
  display: flex;

  margin-bottom: 50px;
}

.calories {
  flex: 1;
  margin-right: 40px;
}

.leaderboardRank {
  flex: 1;
}

.detailsIcon {
  margin-bottom: 24px;
}

.detailsTitle {
  margin-bottom: 24px;

  font-size: 20px;
  line-height: 24px;
  font-weight: 500;

  text-transform: uppercase;
}

.detailsValue {
  font-family: 'heroic';
  font-size: 132px;
  line-height: 170px;
  font-weight: 300;
}

.detailsSubvalue {
  font-family: 'heroic';
  font-size: 80px;
  line-height: 80px;
}

.actions {
  display: flex;
  justify-content: center;
}

.retakeButton {
  border: 2px solid #3d457b;
  border-radius: 10px;
  background: transparent;

  padding-top: 18px;
  padding-bottom: 18px;
  padding-left: 24px;
  padding-right: 24px;

  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  color: #3d457b;
}
