@import 'src/sass/colors';

.video {
  position: relative;
  max-height: 100%;
  max-width: 100%;
  border-radius: 10px;
  margin-top: 35px;
  margin-bottom: 55px;
  overflow: hidden;

  video {
    max-height: 100%;
    max-width: 100%;
  }

  .pauseOverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    align-items: center;
    justify-content: center;

    button {
      background: transparent;
      border-width: 0;
    }
  }

  .topBar {
    position: absolute;
    top: 10px;
    right: 10px;
    display: none;
    z-index: 5;
    color: #fff;
  }

  &.paused {
    .pauseOverlay {
      display: flex;
      pointer-events: auto;
    }
    .topBar {
      display: block;
    }
  }
}

.videoProgressBar {
  position: absolute;
  height: 12px;
  background-color: $pivot-red;
  bottom: 0;
  left: 0;
  width: 0;
  transition: all 1s linear;
}
