@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  font-family: Moderat;
  padding-bottom: 30px;
  background: linear-gradient(44.4deg, #f6fafe 5.96%, #d9e3f0 93%);

  :global(.alert.alert-danger) {
    position: relative;
  }

  .list {
    h1 {
      text-transform: uppercase;
      font-size: 30px;
      font-weight: 300;
      line-height: 36px;
      color: $dark_blue;
      margin: 0;
      padding: 50px 0px 15px 30px;
      background: linear-gradient(44.4deg, #f6fafe 5.96%, #d9e3f0 93%);
    }

    small {
      display: block;
      text-transform: none;
      font-size: 26px;
      font-weight: 300;
      line-height: 32px;
      color: $dark_blue;
    }

    .customInput {
      display: block;
      background: $white;
      margin: 0;
    }

    .inputList {
      display: block;
      font-size: 30px;
      line-height: 36px;
      list-style: none;
      color: $dark_blue;
      background: $white;
      margin: 0;

      li {
        padding: 24px 0;
        > label {
          display: flex;
          align-items: center;
        }
        & + li {
          border-top: solid 2px #3d457b22;
          margin-right: 40px;
        }
        &:first-child {
          padding-top: 32px;
        }
        &:last-child {
          padding-bottom: 30px;
        }
      }
    }
  }
}

.radio {
  width: 42px;
  margin: 0 22px 0 0;
}

.optionTitle {
  max-width: 930px;
}

.countContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  line-height: normal;
  height: 259px;
  background: $white;
}

.daysContainer {
  background: $white;
  max-height: 158px;
  display: flex;
  justify-content: center;
}
