@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  width: 295px;
  display: flex;
  margin-bottom: 14px;
  font-family: $moderat;
  font-style: normal;

  .imageWrapper {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: relative;

    .image {
      width: 112px;
      height: 75px;
      border-radius: 4px;
      object-fit: cover;
    }

    .lockIcon {
      position: absolute;
      top: 2px;
      left: 2px;
    }
  }

  .info {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-left: 14px;
    font-size: 16px;
    line-height: 22px;
    font-weight: normal;

    .header {
      color: #999999;
    }

    .title {
      color: $white;
    }

    .missed {
      opacity: 0.3;
    }
  }
}
