@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  margin: 30px 30px 0;

  a:hover {
    text-decoration: none;
  }
}

.title {
  margin-top: 30px;
  margin-bottom: 22px;
  font-size: 30px;
  font-weight: 300;
  color: #434c5b;
  text-transform: uppercase;
}

.description {
  margin-bottom: 20px;
  font-size: 18px;
}

.recordsWrapper {
  display: grid;
  grid-template-columns: repeat(3, 320px);
  grid-gap: 20px 30px;
}

.personalRecords {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
