@import 'src/sass/fonts';
@import 'src/sass/colors';

.title {
  @include headling-medium;

  color: $dark_blue;
  margin: 50px 0 0 52px;

  &.inactive {
    margin-bottom: 40px;
  }
}

.status {
  font-family: Moderat;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 29px;

  text-transform: uppercase;
  display: flex;
  align-items: center;

  color: $green;
  margin: 8px 0 30px 52px;

  .icon {
    width: 22px;
    height: 22px;
    margin: 0 8px;
    background: url(../../../assets/images/tick.png) no-repeat center center;
  }
}

.message {
  @include body-l-regular;

  color: $dark_blue;
  margin: 52px;

  &.error {
    color: $pivot-red;
  }
}
