@import 'src/sass/colors';
@import 'src/sass/fonts';

.historyItem {
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  padding: 30px 30px 15px 30px;
  background-color: white;

  .header {
    display: flex;
    align-items: center;
    width: 100%;

    .name {
      font-size: 20px;
      opacity: 0.5;
      color: $dark;
      font-family: $moderat;
      text-transform: uppercase;
      line-height: 1.2;
    }

    .image {
      background-color: #c8d8eb;
      border-radius: 50%;
      width: 60px;
      height: 60px;
      margin-right: 15px;
      text-align: center;

      img {
        object-fit: contain;
        width: 60px;
        height: 60px;
      }
    }
  }

  .data {
    display: flex;
    justify-content: space-between;
    margin-top: 25px;

    .record {
      .title {
        font-family: $moderat;
        font-size: 13px;
        font-weight: 500;
        color: $dark_blue;
        text-transform: uppercase;
      }

      .value {
        font-size: 95px;
        font-family: $heroic;
        font-weight: 300;
        line-height: 1;
        font-stretch: condensed;
        color: $dark;

        .type {
          font-size: 23.4px;
          font-family: $moderat;
          font-weight: 300;
          letter-spacing: -0.39px;
          color: $dark_blue;
          margin-left: -15px;
        }
      }

      .date {
        font-size: 17px;
        font-family: $moderat;
        font-weight: 300;
        color: #bcbfc5;
        margin-top: -15px;
      }
    }
  }
}
