.musicPlayerWrapper {
  position: relative;
  border-top: solid 1px rgba(255, 255, 255, 0.31);

  height: 73px;
  margin: 0 -30px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.volume {
  position: absolute;

  right: 20px;
  bottom: 20px;
}

.bluetooth {
  position: absolute;
  right: 95px;
  bottom: 18px;
  width: 20px;
  height: 34px;
  background-image: url(../../../assets/icons/bluetooth-white.svg);
}

@keyframes grow {
  from {
    height: 0px;
    opacity: 0;
  }

  to {
    height: 117px;
    opacity: 1;
  }
}

@keyframes shrink {
  from {
    height: 117px;
    opacity: 1;
  }

  to {
    height: 0px;
    opacity: 0;
  }
}

.volumeControlsGrow {
  background-color: #292c36;
  margin: 0 -30px;

  display: flex;
  justify-content: center;
  align-items: center;

  animation: grow ease-in forwards;
  animation-duration: 0.3s;
  animation-iteration-count: 1;
}

.volumeControlsShrink {
  background-color: #292c36;
  margin: 0 -30px;

  display: flex;
  justify-content: center;
  align-items: center;
  animation: shrink ease-out forwards;
  animation-duration: 0.3s;
  animation-iteration-count: 1;
}
