@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  display: flex;
  width: 396px;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    width: 396px;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.25);
  }

  .dayCol {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 64px;
    min-height: 90px;
    background: rgba(34, 34, 34, 1);
    padding: 10px 0px;
  }

  .details {
    width: 332px;
    position: relative;
    padding: 16px 16px 5px 16px;

    .dayDetail {
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      color: #757575;
      position: absolute;
      top: 13px;
    }

    .header {
      font-size: 20px;
      line-height: 30px;
      color: $white;
      position: relative;
      width: 245px;
      margin-top: 12px;
      display: flex;
      align-items: flex-end;
    }

    .greyed {
      opacity: 0.3;
    }

    .classesSection {
      margin-top: 15px;
    }
  }
}
