@import '../../sass/fonts';

$violetColor: #3d457b;

.workout-detail {
  .wrapper {
    background: #f1f4f7;
    box-sizing: border-box;
  }

  .header {
    position: relative;
    z-index: 1;

    .image {
      position: relative;

      img {
        height: 720px;
        object-fit: cover;
      }

      &:after {
        content: ' ';
        height: 287px;
        background-image: linear-gradient(to bottom, transparent, black);
        left: 0;
        right: 0;
        bottom: 0;
        /* top: 0; */
        position: absolute;
        display: block;
        z-index: 1;
      }

      > div {
        position: absolute;
        padding: 30px;
        bottom: 0px;
        color: #fff;
        z-index: 2;
      }

      .title {
        font-size: 42px;
        font-weight: normal;
        line-height: 1.2em;
      }

      .trainer--name {
        margin-top: 12px;
        font-size: 22px;
        text-transform: uppercase;
        line-height: 1.2em;
      }

      .description {
        margin-top: 15px;
        font-size: 20px;
        line-height: 1.2em;
      }
    }

    .infos {
      padding: 35px 30px;
      box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.15);
      background: #fff;

      .main-info {
        &--title {
          height: 24px;
          font-family: $moderat;
          font-size: 20px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: normal;
          color: $violetColor;
          text-transform: uppercase;
        }

        &--value {
          font-family: $heroic;
          font-size: 146px;
          font-weight: 300;
          font-style: normal;
          font-stretch: condensed;
          line-height: normal;
          letter-spacing: normal;
          color: $violetColor;
          position: relative;
          display: inline-block;
          min-width: 200px;

          sup {
            font-size: 90px;
          }

          .mode {
            font-size: 20px;
            position: absolute;
            right: 0;
            bottom: 50px;
            text-transform: uppercase;
            font-weight: 400;
            border-radius: 7px;
            background-color: $violetColor;
            color: white;
            padding-top: 5px;
            height: 30px;
            min-width: 60px;
            text-align: center;
          }
        }
      }

      .sec-info {
        svg {
          position: absolute;
          top: 5px;
        }

        &--title,
        &--value {
          margin-left: 28px;
          opacity: 0.6;
          font-family: $moderat;
          font-size: 20px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: 1.5;
          letter-spacing: normal;
          color: #434c5b;
          text-transform: uppercase;
        }

        &--value {
          text-transform: none;
        }
      }
    }
  }

  .plans {
    padding: 25px;
    .plansHeader {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100px;
      background: #fff;
      color: #3d457b;
      font-size: 30px;
      border-radius: 10px;
      margin-bottom: 20px;
    }
  }

  .planContent {
    width: 75vw;
  }

  .planLabel {
    color: #3d4756;
    font-size: 34px;
  }

  .planComments {
    display: flex;
    color: #3d4756;

    ul {
      flex: 1;

      li + li {
        margin-top: 10px;
      }
    }
  }
}
