.about {
  background: #f8f8f8;
  padding-top: 87px;
  min-height: 100%;

  h3 {
    font-weight: normal;
    text-align: center;
  }

  animation: fadein 0.5s;
}

.logo {
  width: 400px;
  display: block;
  margin: 30px auto;
}

.disclaimer {
  font-size: 16px;
  padding: 5px 20px 10px 20px;
  white-space: pre-wrap;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
