.password {
  input[type='text'],
  input[type='password'] {
    border-radius: 10px;
    -webkit-backdrop-filter: blur(4.1px);
    backdrop-filter: blur(4.1px);
    box-shadow: 0 12px 14px 0 rgba(200, 216, 235, 0.3);
    border: solid 2px #c8d8eb;
    background-color: rgba(255, 255, 255, 0.7);
    padding: 30px 24px;
    color: #3d457b;
    margin-bottom: 24px;
    font-size: 26px;
    line-height: 32px;
    height: 92px;
    font-weight: normal;

    &:global(.is-valid) {
      padding-right: 19%;
      background-position: 88% center;
    }
  }

  &:global(.input-group) > :global(.form-control):not(:last-child) {
    border-radius: 10px;
  }

  :global(.input-group-append) {
    z-index: 100;
    position: absolute;
    height: 95px;
    right: 0;
    opacity: 0.5;

    span {
      display: flex;
      align-items: center;
    }
  }
}

.showPass {
  margin-top: 10px;
  //override kilo button icon size
  i {
    width: 2.25rem !important;
    height: 2.25rem !important;
  }
}

//override reset.css
.marginReset {
  margin-bottom: 0;
}
