@import 'src/sass/colors';
@import 'src/sass/fonts';

.listItem {
  display: flex;
  font-family: $moderat;
  margin: 0 30px;
  padding: 30px 0;
  list-style: none;
  border-bottom: solid #3d457b12 2px;
  align-items: center;
  color: #434c5b;
  cursor: pointer;

  &:last-child {
    border-bottom: none;
  }

  .icon {
    width: 50px;
    height: 50px;
    margin-right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    img {
      max-width: 100%;
    }
  }

  .title {
    flex: 1;
    font-size: 30px;
    line-height: 36px;
  }

  .subTitle {
    margin-top: 6px;
    font-size: 25px;
    line-height: 29px;
  }

  .secondaryAction {
    min-width: 50px;
  }

  .rounded {
    border-radius: 50%;
    overflow: hidden;
  }

  .straight {
    border-radius: 0;
  }

  .status {
    font-family: Moderat;
    font-size: 25px;
    line-height: 29px;
    text-transform: uppercase;
    display: flex;
    align-items: center;

    span {
      display: inline-block;
      width: 22px;
      height: 22px;
      margin: 0 8px;
    }

    &-true {
      color: #6ec948;

      span {
        background: url(../../../assets/images/tick.png) no-repeat center center;
      }
    }

    &-false,
    &-undefined {
      color: #3d457b88;

      svg {
        margin: 0 8px;
        font-size: 22px;
      }
    }
  }
}
