@import 'src/sass/colors';

.content {
  width: 720px;
}

.page {
  background-image: linear-gradient(151deg, #ffffff, #c8d8eb);
  overflow: auto;
  height: 100vh;
  font-family: Moderat;
  position: relative;

  & > img {
    position: absolute;
    top: 100px;
    left: -300px;
    z-index: 0;
    opacity: 0.5;
  }
}
