@import 'src/sass/colors.scss';

.dropdown {
  position: relative;
  background-color: #434c5b;
  border: none;
  box-shadow: none;
  height: 114px;
  border-radius: 10px;

  .label {
    display: flex;
    align-items: center;
    height: 114px;
    padding: 0 30px;
    font-size: 30px;
    color: #fff;

    &.placeholder {
      color: #fff;
      opacity: 0.3;
    }
  }

  .options {
    z-index: 1;
    border-radius: 0 0 10px 10px;
    overflow: hidden;
  }

  .list {
    overflow: auto;
    max-height: 615px;

    &::-webkit-scrollbar {
      display: initial;
      width: 18px;
      background-color: white;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 8px;
      border: 6px solid rgba(0, 0, 0, 0);
      background-clip: padding-box;
      background-color: rgba(0, 0, 0, 0.2);
    }
  }

  .arrow {
    position: absolute;
    right: 30px;
  }
}
