@import 'src/sass/fonts';
@import 'src/sass/colors';

.selectables {
  margin-top: 176px;
  width: 648px;
  height: 308px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  .recommended {
    display: flex;
    flex-direction: column;
    align-items: center;
    .chip {
      margin: 0px auto;
      padding: 8px 12px;
      font-size: 22px;
      line-height: 28px;
      font-weight: 500;
      background-color: $white;
      color: $black;
      border-radius: 8px;
      text-transform: uppercase;
      opacity: 0.4;
      width: 193px;
      height: 44px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .selected {
      opacity: 1;
    }
  }
}
