@import 'src/sass/colors';
@import 'src/sass/fonts';

.button {
  position: absolute;
  bottom: 32px;
  left: 32px;
  background-color: $white;
  color: $dark_blue;
  border: none;
  outline: none;
  border-radius: 8px;
  min-width: 80px;
  min-height: 80px;
  padding: 20px;
  font-family: $moderat;
  font-weight: normal;
  font-size: 30px;
  line-height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.15);

  &:focus {
    border: none;
    outline: none;
  }

  .icon {
    margin-top: 1px; //makes the bottom of the text align better with bottom of icon
  }

  .text {
    margin-left: 16px;
  }
}
