@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  width: 100%;
  color: $white;
  font-family: $moderat;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 40px;

  .title {
    margin-top: 26px;
    font-weight: 500;
    font-size: 52px;
    line-height: 62px;
    width: 648px;
  }

  .subtitle {
    margin-top: 6px;
    font-size: 20px;
    line-height: 28px;
  }

  .details {
    margin: 32px 20px 0 0;
  }
}
