@import 'src/sass/colors';

.popup {
  display: flex;
  justify-content: space-between;
  position: absolute;
  left: 49px;
  bottom: 254px;
  height: 79px;
  width: 984px;
  background-color: $base-blue;
  color: white;
  font-size: 26px;
  line-height: 30px;
  font-weight: 500;
  padding: 24px 30px 24px 40px;
  border-radius: 10px;
  z-index: 20;
  animation: fadeIn 5s;

  button {
    all: unset;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
