@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  position: relative;
  display: flex;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  overflow: hidden;
}

.day {
  position: absolute;
  top: 29px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 124px;
  font-size: 24px;
  text-transform: uppercase;
  color: #fff;

  span {
    font-family: $font-numbers;
    font-size: 86px;
    line-height: 0.8em;
  }
}

.thumbnail {
  display: flex;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  width: 424px;
  height: 316px;

  img {
    height: 100%;
    object-fit: cover;
  }
}

.info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 20px 30px 47px;

  button {
    margin-top: 25px;
  }
}

.title0 {
  font-size: 17px;
  text-transform: uppercase;
  color: #3d4756;
  opacity: 0.7;

  span {
    color: #6695cc;
    opacity: 1;
  }
}

.title {
  margin-top: 25px;
  font-size: 30px;
  color: #3d4756;
  overflow: hidden;
}

.subtitle {
  font-size: 17px;
  text-transform: uppercase;
  color: $highlight_blue;
  margin-top: 5px;
}

.description {
  margin-top: 10px;
  font-size: 20px;
  font-weight: 300;
  line-height: 1.55;
  color: #434c5b;
}

.skip {
  display: flex;
  align-items: flex-end;
  padding-right: 22px;
  padding-bottom: 33px;
  font-size: 16px;
  color: #3d4756;
  opacity: 0.5;
  cursor: pointer;
  text-transform: uppercase;
}

.button {
  border-radius: 10px;
  line-height: 1.2em;
  box-shadow: none;
}

.fillRemaining {
  flex-grow: 1;
}

.noShrink {
  flex-shrink: 0;
}
