.divider {
  // bleed to edges
  margin-left: -48px;
  margin-right: -48px;

  border-top: solid 1px rgba(255, 255, 255, 0.3);
}

.header {
  display: flex;
  justify-content: flex-end;
  position: relative;
}

.title {
  position: absolute;
  top: 21px;
  left: 0;
  right: 0;

  padding-left: 24px;
  padding-right: 24px;

  text-align: center;

  font-size: 20px;
  line-height: 24px;
  font-weight: 300;
  font-style: italic;

  color: white;

  pointer-events: none;
  touch-action: none;
}

.actionButton {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 64px;
  height: 64px;

  outline: none;
  border: none;
  background: none;

  &:focus {
    outline: none;
  }
}

.drawer {
  max-height: 0;
  transition: max-height 0.45s ease-out, opacity 0.6s;
  overflow: hidden;
  opacity: 0;

  &.drawerOpen {
    max-height: 250px;
    opacity: 1;
    transition: max-height 0.45s ease-in, opacity 0.6s;
  }
}

.drawerContent {
  display: flex;
  justify-content: center;
  padding-bottom: 24px;
}

.volumeControl {
  display: flex;
  align-items: center;

  .volumeButton {
    width: 54px;
    height: 54px;

    padding: 0;
    margin: 0;

    outline: none;
    border: none;
    background: none;

    transform: scale(1);
    transition: transform 0.6s;

    &:active {
      transform: scale(0.98);
    }

    &:focus {
      outline: none;
    }
  }

  .volumeCount {
    width: 56px;
    text-align: center;
    font-size: 24px;
    color: white;
  }
}
