@import 'src/sass/fonts';
@import 'src/sass/colors';

.container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1500;

  .content {
    border-radius: 10px;
    background-color: #262626;
    width: 918px;
    padding: 80px 32px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $white;
    font-family: $moderat;

    .title {
      text-align: center;
    }

    h1 {
      font-size: 40px;
      font-weight: 500;
    }

    h2 {
      font-weight: normal;
      font-size: 26px;
      line-height: 32px;
      margin-top: 16px;
    }

    .options {
      margin-top: 64px;

      > div {
        margin-bottom: 16px;
      }

      :last-child {
        margin-bottom: 0px;
      }
    }
  }
}
