.container {
  display: flex;
  flex-direction: row-reverse;

  .avatar {
    width: 40px;
    height: 40px;
    border: solid 2px #f5f9fe;
    margin-right: -10px;
    font-size: 12px;
  }
}

.avatarKilo {
  width: 40px;
  height: 40px;
  border: solid 2px var(--color-white);
  background-color: var(--color-dark-blue);
  margin-right: -10px;
  border-radius: 50%;
}
