@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.class-session-item {
  position: relative;
  display: flex;
  height: 200px;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  overflow: hidden;

  .dayStatus {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 124px;

    .checkmark {
      margin-top: 8px;
    }
  }

  .thumbnail {
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;

    width: 300px;

    .complete {
      filter: grayscale(1);
      opacity: 0.5;
    }

    img {
      width: 100%;
    }
  }

  .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px 20px 30px 47px;
  }

  .title {
    margin-top: 8px;
    font-size: 27px;
    font-weight: normal;
    color: #3d4756;
  }

  .description {
    margin-top: 11px;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.55;
    color: #434c5b;
  }

  .lock {
    padding: 22px 22px 0 0;

    svg {
      opacity: 0.5;
      color: #4e538e;
    }
  }

  .status {
    display: flex;
    text-transform: uppercase;
    font-size: 22px;
    font-weight: 300;
    line-height: 28px;
    align-items: center;

    img {
      margin-right: 12px;
    }
    &.completed {
      color: $green;
    }
    &.missed {
      color: $label-gray;
    }
  }
}
