@import '../../sass/fonts';
@import '../../sass/colors';

.calendar {
  display: flex;
  &.loading {
    opacity: 0.5;
  }
  .content {
    flex: 1;
  }

  .title {
    font-size: 40px;
    line-height: 47px;
    color: #434c5b;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 38px;
    margin-bottom: 30px;
  }

  .dayOfWeek {
    display: flex;
    justify-content: space-between;

    span {
      font-size: 22px;
      font-weight: 400;
      line-height: 28px;
      color: $label-gray;
      width: 133px;
      text-align: center;
      text-transform: uppercase;
    }
  }

  .weekDays {
    display: flex;
    justify-content: space-between;

    > div {
      border-bottom: solid 4px transparentize($primary, 1);
      transition: all ease-in-out 500ms;
      position: relative;
      color: #3d4756;
      width: 133px;
      height: 133px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &.deactive {
        color: #cccccc;
        opacity: 0;
        height: 0;
      }
      &.selected {
        border-bottom: solid 4px $primary;
      }
    }

    .month {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      font-size: 15px;
      font-weight: 300;
      text-align: center;
      text-transform: uppercase;
    }

    .date {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 38px;
      height: 38px;
      font-size: 40px;
      font-weight: 300;
      line-height: 47px;
    }

    .today {
      & > span {
        color: $primary;
      }
    }

    .hasWorkouts:after {
      content: '';
      display: block;
      position: absolute;
      bottom: 16px;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: $primary;
      color: #fff;
      cursor: pointer;
    }
  }

  .info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 45px;
    border-left: 1px solid #d8d8d8;

    > div {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .activeDays {
      font-size: 20px;
      color: #3d457b;

      span {
        font-family: $font-numbers;
        font-size: 100px;
        font-weight: 200;
        line-height: 0.75;
      }
    }

    .days {
      margin-top: 75px;
      font-size: 20px;
      color: #7ed321;

      &.less {
        color: $primary;
      }

      span {
        font-family: $font-numbers;
        font-size: 100px;
        font-weight: 200;
        line-height: 0.75;
      }

      div {
        font-size: 17px;
        color: #9b9b9b;
      }
    }
  }
}
