.modal {
  max-width: unset !important;
  width: 816px !important;

  :global {
    .modal-content {
      background: linear-gradient(36.96deg, #f6fafe 5.96%, #d9e3f0 93%);
      border-radius: 10px;
      padding: 48px;

      .modal-body {
        padding: 0px;
      }
    }
  }
}
.content {
  min-height: 400px;
  display: flex;
  flex-direction: column;

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;

    h1 {
      font-family: Moderat;
      font-weight: bold;
      font-size: 40px;
      line-height: 40px;
      color: #434c5b;
    }

    button {
      padding: 0;
    }
  }

  .list ul {
    background: transparent;
    overflow: auto;
    max-height: 820px;

    li {
      margin: 0 -12px 0 0;
      border-bottom: 1px solid #9ea2bd;
    }
  }

  .list {
    margin-bottom: 60px;
  }

  footer {
    margin-top: auto;
    display: flex;
    justify-content: flex-end;
  }
}
