.wrapper {
  text-align: center;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  padding: 0 190px 0 100px;

  display: flex;
  justify-content: center;

  .cover {
    margin-right: 15px;
    margin-top: 5px;
    width: 22px;
    height: 22px;
  }

  .title {
    font-weight: 500;
    font-size: 20px;
  }

  .marquee {
    max-width: 95%;
    margin-top: 2px;
  }
}
