@import 'src/sass/fonts';

.wrapper {
  background: #f1f4f7;
  box-sizing: border-box;
}

.header {
  position: relative;
  z-index: 1;
}

.timer {
  z-index: 2;
  position: absolute;
  top: 0;
}

.startsInTimer {
  display: flex;
  flex-direction: column;
  align-items: center;

  :global(.progressTimerText) {
    color: white !important;
  }

  :global(.progress) {
    background-color: white !important;
  }

  :global(.progressMeter) {
    stroke: white !important;
  }

  :global(.progressValue) {
    stroke: white !important;
  }
}

.startLabel {
  text-transform: uppercase;
  font-size: 22px;
  line-height: 28px;
  white-space: nowrap;
}

.image {
  background: black;
  width: 100%;
  min-height: 720px;
  position: relative;
  line-height: 1.55;
  font-weight: lighter;

  &:after {
    content: ' ';
    height: 287px;

    $image-overlay-color: #111;
    background-image: linear-gradient(to bottom, transparentize($image-overlay-color, 1), $image-overlay-color);
    left: 0;
    right: 0;
    bottom: 0;
    /* top: 0; */
    position: absolute;
    display: block;
    z-index: 1;
  }

  > div {
    position: absolute;
    bottom: 0px;
    padding: 30px 60px 50px;
    color: #fff;
    z-index: 2;
  }

  img {
    width: 100%;
  }
}

.title {
  font-size: 45px;
  font-weight: bold;
  line-height: 115%;
}

.datetime {
  margin-top: 12px;
  font-size: 22px;
  font-weight: normal;
  text-transform: uppercase;
}

.description {
  margin-top: 15px;
  font-size: 20px;
}

.tags {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 140px;
  box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.15);
  background: #fff;
}

.tabs {
  justify-content: center;
  border-bottom: transparent 4px solid;
  margin-top: 30px;
  margin-bottom: 24px !important;

  :global(.nav-link) {
    color: #434c5b88;
    padding: 16px 25px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;

    &.active {
      color: #434c5b;
      border-bottom: #434c5b 4px solid;
    }
  }
}

.tabContent {
  margin-right: 24px;
  margin-left: 24px;
}
