@import 'src/sass/colors.scss';

.container {
  position: relative;

  button {
    position: absolute;
    left: 36px;
    bottom: 36px;
    display: flex;
    justify-content: center;
    height: 64px;
    width: 272px;
    padding: 20px;
    border-radius: 8px;
    border: none;
    font-size: 17px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: $white;

    svg {
      margin-left: 10px;
    }
  }
}
