$border-color: #434c5b55;

.wrapper {
  background: #fff;
  margin: 25px;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0px rgba(104, 114, 169, 0.2);
  display: flex;
  flex-direction: column;

  header {
    display: flex;
    padding: 28px 0px 22px 80px;
    margin: 0 44px;
    border-bottom: solid 1px $border-color;
    justify-content: flex-end;
    font-size: 18px;
    color: #3f427e;
    & > div {
      display: flex;
      align-items: center;
    }
  }
  ul {
    padding: 0 0 15px;
    flex-grow: 1;
    overflow-y: scroll;
    font-size: 26px;
    color: #434c5c;

    .rankRow {
      display: flex;
      border-bottom: solid $border-color 1px;
      padding: 28px 90px 22px;
      justify-content: space-between;
      align-items: center;

      &.centered {
        justify-content: center;
      }
    }
  }

  .userCard {
    display: flex;
    align-items: center;
    min-width: 400px;

    .name {
      display: inline-block;
      line-height: 1.08;
      border-bottom: 1px solid currentColor;
    }

    > * {
      margin: 0 46px 0 0;
    }

    span {
      display: inline-block;
      min-width: 50px;
      font-weight: 400;
      text-align: center;

      &.hide {
        min-width: unset;
      }
    }

    .avatarIcon {
      width: 58px;
      height: 58px;
    }
  }

  .loading {
    text-align: center;
    width: 100%;
    img {
      width: 50px;
    }
  }

  .modeControl {
    display: flex;
    flex-grow: 0;
    cursor: pointer;
    height: 30px;
    border-radius: 10px;
    border: solid #4b5385 1px;
    .item {
      height: 28px;
      min-width: 110px;
      text-transform: uppercase;

      display: flex;
      justify-content: center;
      align-items: center;

      color: #4b5385;

      border-radius: 8px;
      transition: all ease-in-out 200ms;
    }

    .activeItem {
      color: white;
      background-color: #4b5385dd;
    }
  }
}
