@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  width: 640px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: $white;
  margin-top: 42px;
  margin-bottom: 36px;

  .button {
    background: $black;
    border: none;
    &:focus {
      outline: none;
    }

    .circle {
      height: 75px;
      width: 75px;
      border-radius: 50%;
      border: 6px solid $white;
      opacity: 0.6;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .day {
      color: $white;
      opacity: 0.6;
      text-transform: capitalize;
      font-size: 26px;
      line-height: 32px;
      margin-top: 8px;
    }

    .active {
      opacity: 1;
    }
  }
}
