@import 'src/sass/colors';

.skillLevelContainer {
  display: flex;
  flex-direction: column;
  height: 576px;

  h2 {
    font-size: 16px;
    color: $label-gray;
    text-transform: uppercase;
    margin-bottom: 16px;
    padding-top: 56px;

    // adds horizontal line break around text
    width: 100%;
    text-align: center;
    border-bottom: 1px solid $label-gray;
    line-height: 0.1em;
    margin: 10px 0 20px;

    span {
      background: $black;
      padding: 0 20px;
    }
  }

  div {
    margin-top: 16px;
  }
}

.summaryCard {
  h1 {
    width: 700px;
  }
  padding: 100px 0px;
}
