@import 'src/sass/fonts';
@import 'src/sass/colors';

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.trainWithOthers {
  @include body-sm-regular;
  color: $dark_blue;
  margin-bottom: 12px;
}

.link {
  font-family: $moderat;
  font-size: 18px;
  font-weight: 400;
  color: $dark_blue;
  text-transform: uppercase;
  text-decoration: underline;
}

.textBubble {
  font-family: $moderat;
  font-size: 16px;
  font-weight: 500;

  color: $dark_blue;
  text-transform: uppercase;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 292px;

  background-color: #c8d8eb;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  z-index: 1;
}

.randomGroupAndCount {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.remainingGroupsCircle {
  width: 40px;
  height: 40px;
  background-color: $brand-blue;
  color: $white;
  border-radius: 50%;
  line-height: 40px;
  text-align: center;
  margin-left: -8px;
  z-index: 0;
}
