.skeletonPositionComponent {
  position: relative;

  background: gray;
  width: 300px;
  height: 300px;

  $skeleton-radius: 15px;
  .skeleton {
    color: white;
    text-align: center;
    font-size: 10px;

    position: absolute;

    .icon {
      width: $skeleton-radius;
      height: $skeleton-radius;

      margin-left: -(calc($skeleton-radius/2));
      margin-top: -(calc($skeleton-radius/2));

      border-radius: 100%;
      border-color: transparent;
    }

    &.tracking .icon {
      border: 3px solid green;
    }

    &.isolated .icon {
      border: 3px solid blue;
    }
  }

  .yogaMat {
    background: #333;
    position: absolute;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
  }
}
