@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  height: 256px;
  width: 224px;
  background-color: $white;
  border-radius: 8px;
  font: $moderat;
  color: #3a4376;
  border: 3px solid transparent;
  opacity: 0.6;

  &.selected {
    opacity: 1;
    border-color: #3a4376;

    .check {
      visibility: visible;
    }
  }

  .imagePlaceholder {
    height: 160px;
    width: 160px;

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  .name {
    font-size: 16px;
    font-weight: 500;
    margin-top: 4px;
  }

  .check {
    position: absolute;
    top: 18px;
    right: 16px;
    height: 24px;
    width: 24px;
    visibility: hidden;
  }

  .quantityWrapper {
    display: flex;
    align-items: center;
    margin-top: 4px;

    .quantity {
      font-size: 22px;
      font-weight: 500;
      width: 46px;
      text-align: center;
    }

    .button {
      all: unset;
      height: 24px;
      width: 24px;
      border: 2px solid #9ea2bd;
      border-radius: 50%;
      margin: auto;
      text-align: center;
      z-index: 10;
      line-height: 11px;

      svg {
        vertical-align: baseline;
      }
      &.disabled {
        opacity: 0.6;
      }
    }
  }
}

.equipmentItem {
  position: relative;
  opacity: 0.6;

  &.selected {
    opacity: 1;
  }

  .check {
    position: absolute;
    right: 12px;
    top: 12px;
  }

  .btn {
    display: flex;
    padding: 0;
    border: 0;
    background-color: transparent;

    &.disabled {
      opacity: .6;
    }
  }

  .fontWeight500 {
    font-weight: 500;
  }
}
