@import 'src/sass/colors';

.header {
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
  margin-bottom: 10px;
  color: $dark_blue;
}

.classes {
  display: flex;
  width: 100%;
  overflow-y: scroll;

  .class {
    display: flex;
    flex-direction: column;
    margin-right: 10px;

    &:last-child {
      padding-right: 10px;
    }

    img {
      width: 250px;
      height: 160px;
      border-radius: 8px;
    }

    .instructor {
      color: $dark_blue;
      margin-top: 5px;
      font-size: 15px;
      text-transform: uppercase;
      padding-bottom: 5px;
      border-bottom: 1px solid rgba(67, 76, 91, 0.5);
      width: 220px;
      opacity: 0.9;
    }

    .rankContainer {
      margin-bottom: 20px;

      .leaderboardTitle {
        font-weight: 500;
        font-size: 13px;
        text-transform: uppercase;
        margin-top: 10px;
        color: $dark_blue;
      }

      .rank {
        font-size: 18px;
        font-weight: 300;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 90%;
        color: $dark_blue;
      }

      .grey {
        opacity: 0.5;
      }
    }
  }
}
