@import '../../sass/fonts';
@import '../../sass/colors';
// default values

// 597 = Math.round(2 * Math.PI * 95)

$steps: 597;

$rest-animation-duration: 1267ms;
$set-animation-duration: 1200ms;

/* The radius of the progress meter. */
$progressRadius: 95px;
$progressStrokeWidth: 12px;
$progressTrackStrokeWidth: 3px;

$explosionWidth: 12px;

/* Animation parameters */
$translatePosition: 107px;

@keyframes rest-ready-progress {
  0%,
  100% {
    transform: translate($translatePosition, $translatePosition) rotate(270deg) scale(1);
  }

  12% {
    transform: translate($translatePosition, $translatePosition) rotate(270deg) scale(0.97);
  }

  41% {
    transform: translate($translatePosition, $translatePosition) rotate(270deg) scale(1.05);
  }
}

@keyframes rest-ready-meter {
  26% {
    stroke-opacity: 1;
  }

  52% {
    stroke-opacity: 0.3;
  }
}

.timerWrapper {
  position: relative;
  display: inline-flex;
  text-align: center;
  margin-right: 20px;
  align-self: flex-start;

  &.broadcast-timer {
    zoom: 0.55;
    align-self: center;
    margin-left: 40px;

    .progressTimerText {
      color: $dark;
      top: 28px;
      left: 12px;
      font-size: 100px;
    }
    .progress {
      background-color: $dark;
    }
    .progressMeter {
      stroke: $dark;
    }
    .progressValue {
      stroke: $dark;
    }
  }

  .progressTimerText {
    position: absolute;
    top: 32px;
    right: 25px;
    bottom: 45px;
    left: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $font-numbers;
    font-size: 110px;
    color: #fff;
    overflow: hidden;
    font-weight: 300;
    padding-top: 25px;

    > span {
      margin-top: 20px;
    }

    .timerSeconds {
      display: inline-block;
      width: 75px;

      // important - 10s place changes slower, so no shaking after each change!
      text-align: left;
    }

    .timerSecondsNew {
      width: auto;
    }

    .timerMinutes {
      display: inline-block;
      text-align: right;
    }

    &.restTimerText {
      position: absolute;
      top: 0;
      bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding-top: 0;
      color: #fff;
      opacity: 1;

      .restText {
        font-size: 20px;
        font-weight: 300;
        font-family: $moderat;
        text-transform: uppercase;
      }

      .restTextNew {
        font-weight: 700;
        font-size: 25px;
        line-height: 32px;
        font-family: $moderat;
        color: var(--color-base-70);
      }

      .timerText {
        line-height: 110px;
        height: 110px;
      }

      animation-name: rest-timer-text1, rest-timer-text2;
      animation-delay: 0s, 367ms;
      animation-duration: 367ms, 367ms;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes rest-timer-text1 {
        0%,
        100% {
          opacity: 0;
          transform: scale(0.9);
        }
      }

      @keyframes rest-timer-text2 {
        0% {
          opacity: 1;
          transform: scale(0.9);
        }
      }
    }

    &.restTimerTextNew {
      bottom: auto;
      top: 45px;
    }
  }

  .progressTimerTextNew {
    font-family: $moderat monospace;
    font-weight: 400;
    font-size: 56px;
    line-height: 66px;
    padding-top: 0;
  }

  /* The circle that splashes in the middle of the text element in a rest */
  .restAnimationSplashCircle {
    width: 30px;
    height: 30px;
    border: 0px solid #fff;
    position: absolute;
    border-radius: 50%;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;

    animation-name: rest-timer-mask1, rest-timer-mask2;
    animation-delay: 0s, 200ms;
    animation-duration: 200ms, 333ms;
    animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

    @keyframes rest-timer-mask1 {
      0%,
      100% {
        width: 0;
        height: 0;
      }
    }

    @keyframes rest-timer-mask2 {
      0% {
        width: 0;
        height: 0;
        border-width: 0;
      }

      50% {
        border-width: 55px;
      }

      100% {
        width: 160px;
        height: 160px;
        border-width: 0;
      }
    }
  }

  .timerElapsedAnimationSplashCircle {
    r: $explosionWidth;

    transform: translateX($progressRadius);

    stroke: white;
    stroke-width: 0;

    animation-name: elapsed-splash-timer-mask1, elapsed-splash-timer-mask2;
    animation-delay: 0s, 133ms;
    animation-duration: 200ms, 333ms;
    animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

    @keyframes elapsed-splash-timer-mask1 {
      0%,
      100% {
        r: 0;
      }
    }

    @keyframes elapsed-splash-timer-mask2 {
      0% {
        r: 0;
        stroke-width: 0;
      }

      50% {
        r: $explosionWidth;
        stroke-width: calc($explosionWidth / 2);
      }

      100% {
        r: $explosionWidth;
        stroke-width: 0;
      }
    }
  }

  /* The colored ring indicating the amount of time remaining */
  .progress {
    fill: none;
    stroke-linecap: round;
    transform: translate($progressRadius + $progressStrokeWidth, $progressRadius + $progressStrokeWidth) rotate(270deg);
  }

  /* The semitransparent "track" that the progress meter travels around. */
  .progressMeter {
    r: $progressRadius;
    stroke: #ffffff;
    stroke-opacity: 0.3;
    stroke-width: $progressTrackStrokeWidth;
    stroke-dasharray: $steps;
  }

  .progressValue {
    r: $progressRadius;
    stroke-width: $progressStrokeWidth;
    stroke-dasharray: $steps;
  }

  &.state-set.firstTime {
    .timerMinutes {
      animation-name: state-set-ready-minutes1, state-set-ready-minutes2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-minutes1 {
        0%,
        100% {
          opacity: 0;
        }
      }

      @keyframes state-set-ready-minutes2 {
        0% {
          transform: translateY(-100%);
        }
      }
    }

    .timerSeconds {
      animation-name: state-set-ready-seconds1, state-set-ready-seconds2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-seconds1 {
        0%,
        100% {
          opacity: 0;
        }
      }

      @keyframes state-set-ready-seconds2 {
        0% {
          transform: translateY(100%);
        }
      }
    }

    .timerColon {
      animation-name: state-set-ready-colon1, state-set-ready-colon2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-colon1 {
        0%,
        100% {
          opacity: 0;
          transform: scale(0);
        }
      }

      @keyframes state-set-ready-colon2 {
        0% {
          opacity: 0;
        }
      }
    }

    .progressMeter {
      animation: state-set-ready-meter 1s cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-meter {
        0% {
          transform: rotate(-90deg);
          stroke-dashoffset: $steps;
        }
      }
    }

    .progressValue {
      stroke: url(#gradient);
      animation-name: state-set-ready-value1, state-set-ready-value2;
      animation-delay: 0s, 133ms;
      animation-duration: 133ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-value1 {
        0%,
        100% {
          stroke-dashoffset: $steps;
        }
      }

      @keyframes state-set-ready-value2 {
        0% {
          transform: rotate(-90deg);
          stroke-dashoffset: $steps;
        }
      }
    }
  }

  &.state-set {
    .timerMinutes {
      animation-name: state-set-ready-minutes1, state-set-ready-minutes2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-minutes1 {
        0%,
        100% {
          opacity: 0;
        }
      }

      @keyframes state-set-ready-minutes2 {
        0% {
          transform: translateY(-100%);
        }
      }
    }

    .timerSeconds {
      animation-name: state-set-ready-seconds1, state-set-ready-seconds2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-seconds1 {
        0%,
        100% {
          opacity: 0;
        }
      }

      @keyframes state-set-ready-seconds2 {
        0% {
          transform: translateY(100%);
        }
      }
    }

    .timerColon {
      animation-name: state-set-ready-colon1, state-set-ready-colon2;
      animation-delay: 0s, 200ms;
      animation-duration: 200ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-colon1 {
        0%,
        100% {
          opacity: 0;
          transform: scale(0);
        }
      }

      @keyframes state-set-ready-colon2 {
        0% {
          opacity: 0;
        }
      }
    }

    .progressValue {
      stroke: url(#gradient);
      animation-name: state-set-ready-value1, state-set-ready-value2;
      animation-delay: 0s, 133ms;
      animation-duration: 133ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes state-set-ready-value1 {
        0%,
        100% {
          stroke-dashoffset: $steps;
        }
      }

      @keyframes state-set-ready-value2 {
        0% {
          transform: rotate(-90deg);
          stroke-dashoffset: $steps;
        }
      }
    }

    &.progress {
      animation: rest-ready-progress 1133ms cubic-bezier(0.7, 0, 0.3, 1);
    }

    .progressMeter {
      stroke-dashoffset: 0;
      animation: rest-ready-meter 1133ms cubic-bezier(0.7, 0, 0.3, 1);
    }
  }

  // rest - ready

  &.rest {
    .progress {
      animation: rest-ready-progress 1133ms cubic-bezier(0.7, 0, 0.3, 1);
    }

    .progressMeter {
      stroke-dashoffset: 0;
      animation: rest-ready-meter 1133ms cubic-bezier(0.7, 0, 0.3, 1);
    }

    .progressValue {
      stroke: #ffffff;
      animation-name: rest-ready-value1, rest-ready-value2;
      animation-delay: 0s, 267ms;
      animation-duration: 267ms, 1s;
      animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);

      @keyframes rest-ready-value1 {
        0%,
        100% {
          stroke-dashoffset: $steps;
        }
      }

      @keyframes rest-ready-value2 {
        0% {
          transform: rotate(-90deg);
          stroke-dashoffset: $steps;
        }
      }
    }
  }
}

.timerWrapperNew {
  margin-top: -1rem;
}

.circular-progress {
  position: absolute;
  right: 0;
  top: 236px;
}
