@import 'src/sass/fonts';

.container {
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  margin: 10px 30px 60px 30px;
  padding: 20px 30px 60px 30px;

  .toggleContainer {
    display: flex;

    .toggle {
      padding: 7px 40px;
      border-radius: 30px;
      font-size: 20px;
      color: #98a0bc;
      text-transform: uppercase;

      &.active {
        background-color: #98a0bc;
        color: white;
      }
    }
  }

  .metrics {
    margin: 60px 80px 0px 80px;
    display: flex;
    justify-content: space-between;

    .metric {
      color: #3d457b;

      .value {
        font-family: $heroic;
        font-size: 125px;
        font-weight: 300;
        line-height: 0.97;
        font-stretch: condensed;
        text-align: right;

        .arrow {
          margin-bottom: -29px;
          margin-left: 5px;
        }
      }

      .title {
        font-family: $moderat;
        font-size: 20px;
        text-transform: uppercase;
        text-align: right;
        margin-top: -15px;

        .positive {
          color: #88ca3e;
        }

        .negative {
          color: #98a0bc;
        }
      }
    }
  }
}

// kilo overhaul
.wrapper {
  box-shadow: 0 2px 4px #c8d8eb;
}

.metricValue {
  font-family: $heroic;
  font-weight: 300;
  font-size: 125px;
  line-height: .97;
  font-stretch: condensed;
  text-align: right;
}

.metricArrow {
  margin-bottom: 29px;
}
