@import 'src/sass/colors';

.wrapper {
  margin-top: 30px;
}
.slider {
  display: grid;
  grid-gap: 25px;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  overflow-x: scroll;
  padding-left: 30px;
}

.program {
  width: 400px;
  border-radius: 5px;
  height: 237px;
  padding: 31px 24px 23px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #c8d8eb;

  img {
    position: absolute;
    top: 0%;
    left: 0%;
    color: transparent;
    width: auto;
    height: 100%;
    opacity: 0.73;
  }

  &.light {
    border: solid 1px #c8d8eb;
    background-image: linear-gradient(120deg, #f6fafe 13%, #d9e3f0);
    opacity: 1;

    .instructor {
      color: #434c5b;
    }

    .title {
      color: $highlight_blue;
    }

    .tab {
      color: $blue;
    }
  }

  &.dark {
    border: solid 1px #c8d8eb;
    background-image: linear-gradient(120deg, #f6fafe 13%, #d9e3f0);

    .instructor {
      color: #434c5b;
    }

    .title {
      color: $highlight_blue;
    }

    .tab {
      color: $blue;
    }
  }

  .instructor {
    position: absolute;

    text-transform: uppercase;
    font-size: 16px;
    opacity: 0.73;
  }

  .title {
    position: absolute;
    top: 25%;
    text-transform: uppercase;
    font-size: 24px;
    overflow: hidden;
    line-height: normal;
    max-height: 110px;
    width: 48%;
    text-overflow: ellipsis;
  }

  .info {
    position: absolute;
    bottom: 5%;

    .tab {
      width: 142px;
      height: 48px;
      font-size: 15px;
      display: inline-block;
      margin-right: 42px;
      line-height: 1.5;

      .label {
        font-size: 14px;
        font-weight: 500;
      }
    }
  }
}
