@import 'src/sass/colors.scss';

.section {
  background-color: $light-bg;
  margin-bottom: 45px;
}

.title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  > h3 {
    color: #434c5b;
    font-size: 16px;
    font-weight: 500;
    background-color: #c8d8eb;
    display: inline-block;
    padding: 9px 22px;
    border-radius: 18px;
    text-transform: uppercase;
  }
}

.noFavorites {
  width: 100%;
  height: 240px;
  background-color: #d9dfe6;
  border-radius: 10px;
  color: #434c5b;
  font-size: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;

  .content {
    text-align: center;

    > svg {
      margin-bottom: 12px;
    }
  }
}
