.circle {
  background: #c8d8eb;
  width: 42px;

  display: table-cell;
  vertical-align: middle;
  text-align: center;

  border-radius: 50%;
  height: 42px;
  line-height: 1.8em;
  font-size: 22px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #434c5b;

  position: relative;
  &.sm {
    width: 25px;
    height: 25px;
    font-size: 14px;
  }

  &.green {
    background: rgb(136, 202, 62);
    color: #fff;

    svg {
      overflow: visible;
      width: 1em;
      display: inline-block;
      font-size: inherit;
      height: 1em;
      vertical-align: -0.125em;
    }
  }

  &.red {
    background: #f9423a;
    color: #fff;

    svg {
      overflow: visible;
      width: 0.6875em;
      display: inline-block;
      font-size: inherit;
      height: 1em;
      vertical-align: -0.125em;
    }
  }
}

.correctedTick {
  height: 25px;
  width: 50px;

  span {
    position: absolute;
    bottom: 0;
  }

  .red {
    svg {
      margin-top: 5px;

      width: 15px;
      height: 15px;
    }
  }

  .red:after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    background: linear-gradient(90deg, transparent, transparent, rgba(0, 0, 0, 0.6), #000);
    left: 0;
    border-radius: 50%;
  }

  .green {
    left: 18px;

    svg {
      margin-top: 6px;
    }
  }
}
