.weights {
  position: relative;
}

.weight {
  animation-name: rest-icon-scale1, rest-icon-scale2;
  animation-delay: 0s, 333ms;
  animation-duration: 333ms, 0.6s;
  animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  position: relative;

  @keyframes rest-icon-scale1 {
    0%,
    100% {
      transform: scale(0);
    }
  }

  @keyframes rest-icon-scale2 {
    0% {
      transform: scale(0);
    }
  }
}

.weightQuantity {
  color: var(--color-white);
  position: absolute;
  background: var(--color-base-20);
  border-radius: 4px;
  min-width: 25px;
  text-align: center;
  right: -10px;
  top: -7px;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  padding: 2px 4px 3px;
}
