.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 97%;
  width: 100%;

  .header {
    text-align: center;
    color: #3d4756;
    font-size: 50px;
    line-height: 1.36;
    margin-top: 40px;
    font-weight: 500;
  }
}

.button {
  position: absolute;
  left: 3rem;
  top: 1.5rem;
}

.modal {
  max-height: none;
  margin-top: 10.25rem;
}

/* reset margin of MiniVideoPlayer */
.video {
  background: black;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;

  > div {
    margin: 0;
  }
}
