@import '../../sass/colors';

.schedulePage {
  background-image: linear-gradient(150deg, #f6fafe, #d9e3f0);
}
.scheduleContent {
  padding: 0 50px;
}

.placeholder {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: $dark-gray;

  h1 {
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 12px;
  }

  p {
    font-size: 26px;
    margin-bottom: 32px;
  }
}

.classList {
  border-radius: 10px;
  padding: 15px 0;

  .date {
    margin-bottom: 24px;
    font-size: 26px;
    line-height: 32px;
    font-weight: 500;
    color: $dark_blue;
    text-transform: uppercase;
    padding-top: 30px;
    margin-top: 30px;
  }
}
