@import 'src/sass/colors';
@import 'src/sass/fonts';

.container {
  width: 100%;
}

.section {
  min-height: 107px;
  padding-left: 30px;
  padding-top: 40px;
  padding-bottom: 15px;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.title {
  font-family: $moderat;
  font-size: 30px;
  font-weight: 300;
  line-height: 36px;
  color: #434c5b;
  text-transform: uppercase;
}

.description {
  @include body-l-regular;
  color: #434c5b;
  margin-top: 8px;
}

.children {
  background: #fff;
  padding-inline-start: 0;
  margin: 0;
  position: relative;
}
