@import 'src/sass/fonts';

.wrapper {
  width: 480px;
  height: 210px;
  color: white;
  opacity: 1;
  transition: opacity 250ms;
}

.fadedOut {
  opacity: 0.6;
}

.header {
  display: flex;
  text-transform: uppercase;

  .title {
    font-family: $moderat;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 1px;
  }

  .status {
    margin-left: auto;
    font-family: $moderat;
    font-size: 24px;
    line-height: 28px;
    opacity: 0.8;
  }
}

.content {
  display: flex;
  margin-top: 12px;
}

.info {
  margin-top: 20px;
  width: 176px;
}

.currentHeartRateContainer {
  display: flex;
}

.currentHeartRate {
  font-family: $heroic;
  font-size: 80px;
  line-height: 80px;
  min-width: 82px;
}

.heartRateIcon {
  margin-left: 10px;
  margin-top: 22px;
}

.zone {
  margin-top: 8px;
  font-family: $moderat;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  opacity: 0.8;
}

.zoneSegment {
  position: relative;
  width: 60px;
  margin-right: 4px;
}

.targetShadowLeft {
  position: absolute;
  top: 64px;
  left: 44px;
  width: 16px;
  height: 16px;
  background: linear-gradient(105.48deg, rgba(0, 0, 0, 0) 25.9%, rgba(0, 0, 0, 0.58) 117.17%);
}

.targetShadowRight {
  position: absolute;
  width: 16px;
  top: 64px;
  height: 16px;
  background: linear-gradient(105.48deg, rgba(0, 0, 0, 0) 25.9%, rgba(0, 0, 0, 0.58) 117.17%);
  transform: rotate(-180deg);
}

.ticker {
  position: absolute;
  width: 2px;
  background-color: white;
  z-index: 1; // ticker is always on top

  &.tickerSmall {
    top: 61px;
    height: 22px;
  }

  &.tickerLarge {
    top: 51px;
    height: 42px;
  }
}

.tickerBackground {
  position: absolute;
  background-color: #363636;

  &.tickerBackgroundSmall {
    top: 64px;
    height: 16px;
  }

  &.tickerBackgroundLarge {
    top: 54px;
    height: 36px;
  }
}

.zoneBoxSmall {
  height: 16px;
  margin: 64px 0px 28px 0px;
}

.zoneBoxLarge {
  height: 36px;
  margin: 54px 0px 18px 0px;

  &.zoneBoxHighlight {
    &.zone2 {
      box-shadow: 0px 0px 31px #46b6c7;
    }

    &.zone3 {
      box-shadow: 0px 0px 31px #9eda40;
    }

    &.zone4 {
      box-shadow: 0px 0px 31px #ff8230;
    }

    &.zone5 {
      box-shadow: 0px 0px 31px #ff3a28;
    }
  }
}

.zone1 {
  background-color: #929090;
}

.zone2 {
  background-color: #65aab4;
}

.zone3 {
  background-color: #a4c373;
}

.zone4 {
  background-color: #e38d54;
}

.zone5 {
  background-color: #e15447;
}

.zoneInactive {
  background-color: #363636;
}

.zoneNumber {
  width: 60px;
  height: 28px;
  text-align: center;
  font-family: $moderat;
  font-size: 22px;
  line-height: 28px;
  opacity: 0.4;
}

.zoneNumberHighlight {
  width: 60px;
  height: 28px;
  text-align: center;
  font-family: $moderat;
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
}
