.icon {
  margin-bottom: 10px;
  overflow-y: hidden;
}

.pulsing {
  animation: pulse 1s infinite 1201ms;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  23% {
    transform: scale(0.9);
  }
  43% {
    transform: scale(1.1);
  }
  73% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}
