.component {
  left: calc(50% - 175px);
  width: 350px;
  color: white;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}

.label {
  // keep the space for the text, even before the text appears.
  // adapted from https://stackoverflow.com/a/33298969
  &:empty::after {
    content: '.';
    visibility: hidden;
  }
}
