@import 'src/sass/colors.scss';

.classes-page {
  &.wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-image: linear-gradient(150deg, #f6fafe, #d9e3f0);
  }

  &--wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }

  &--content {
    flex: 1;
    padding: 48px;
    background: $light-bg;
    overflow-y: auto;

    .searchResults {
      display: grid;
      grid-template-columns: auto auto auto;
      justify-content: space-between;

      & > * {
        margin-bottom: 48px;
      }
    }

    &::-webkit-scrollbar {
      display: none;
    }

    > h1 {
      font-size: 30px;
      font-weight: 400;
      margin: 0;
      color: $dark_blue;
    }
  }
}
