@import 'src/sass/colors';

.classAttributes {
  color: $white;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 16px;

  > div {
    min-width: 125px;
  }

  > :not(:last-child) {
    margin-right: 40px;
  }

  .title {
    text-transform: uppercase;
    color: #a9a9a9;
  }

  p {
    margin-bottom: 0;
  }
}
