@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.muscleGroupPlaceholder {
  position: relative;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  padding: 10px;
  height: 278px;

  .dashes {
    text-align: center;
    padding-top: 50px;
    font-weight: 300;
    font-size: 135px;
    line-height: 0.97;
    color: $dark;
    font-family: $heroic;
    margin-top: 5px;
  }

  .muscleGroupNameContainer {
    position: absolute;
    bottom: 10px;
    text-align: center;

    border-top: 1px solid $dark;
    padding-top: 15px;
    width: 300px;

    .groupName {
      opacity: 0.7;
      font-family: $moderat;
      color: $dark;
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;
    }
  }
}

.muscleGroup {
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  padding: 10px;

  .valueContainer {
    display: flex;
    justify-content: center;

    padding-top: 30px;
    font-weight: 300;

    .value {
      font-size: 135px;
      line-height: 0.97;
      color: $dark;
      font-family: $heroic;
      margin-top: 5px;
    }

    .changeContainer {
      display: flex;
      flex-direction: column-reverse;
      margin-left: 15px;
      margin-bottom: 20px;

      .lbs {
        font-size: 30px;
        letter-spacing: -0.45px;
        color: #4c558c;
        font-family: $moderat;
      }

      .change {
        color: #88ca3e;
        font-size: 27px;
        font-weight: 400;
      }

      .indicator {
        display: flex;
        flex-direction: column;
        padding: 0;
        list-style: none;
        margin: 0;
        align-items: center;
        width: 10px;

        & > li {
          border-radius: 50%;
          background-color: #88ca3e;
          width: 5px;
          height: 5px;
          margin-bottom: 8px;
          opacity: 0.5;
        }

        & > li:first-child {
          width: 7px;
          height: 7px;
          opacity: 1;
        }

        & > li:last-child {
          width: 3px;
          height: 3px;
          opacity: 0.4;
          margin-bottom: 0;
        }
      }
    }
  }

  .name {
    text-align: center;
    font-family: $moderat;
    opacity: 0.5;
    color: $dark;
    font-size: 20px;
    text-transform: uppercase;
    padding-bottom: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .seeAllContainer {
    padding-top: 15px;
    border-top: 1px solid $dark;

    display: flex;
    flex-direction: row-reverse;

    .seeAll {
      font-family: $moderat;
      color: $dark;
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;

      &:after {
        content: ' ';
        display: inline-block;
        margin-left: 8px;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-left: 10px solid $dark;
      }
    }
  }
}

.muscleGroupRecord {
  box-shadow: 0 2px 4px #c8d8eb;

  .value {
    font-size: 135px;
    line-height: 0.97;
    font-family: $heroic;
    font-weight: 200;
  }

  .indicator {
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    width: 7px;
    padding: 0 0 0 6px;
    margin: 0;

    & > li {
      border-radius: 50%;
      background-color: var(--color-green);
      width: 5px;
      height: 5px;
      margin-bottom: 4px;
    }

    & > li:first-child {
      width: 7px;
      height: 7px;
    }

    & > li:last-child {
      width: 3px;
      height: 3px;
      margin-bottom: 0;
    }
  }

  .exerciseName {
    border-bottom: 1px solid var(--color-base-60);
  }
}

.muscleGroupRecordPlaceholder {
  box-shadow: 0 2px 4px #c8d8eb;

  .dashes {
    font-size: 135px;
    line-height: 0.97;
    font-family: $heroic;
    font-weight: 200;
  }

  .muscleGroupName {
    border-top: 1px solid var(--color-base-60);
  }
}
