@import '../../sass/fonts';

.class-page-detail {
  .wrapper {
    background: #f1f4f7;
    box-sizing: border-box;
  }

  .header {
    position: relative;
    z-index: 1;

    .image {
      background: black;
      width: 1080px;
      min-height: 720px;

      position: relative;
      &:after {
        content: ' ';
        height: 287px;

        $image-overlay-color: #111;
        background-image: linear-gradient(to bottom, transparentize($image-overlay-color, 1), $image-overlay-color);
        left: 0;
        right: 0;
        bottom: 0;
        /* top: 0; */
        position: absolute;
        display: block;
        z-index: 1;
      }
      > div {
        position: absolute;
        bottom: 0px;
        padding: 30px 60px 50px;
        color: #fff;
        z-index: 2;
      }
      .timer {
        top: 0;
        bottom: auto;
        display: flex;
        flex-direction: column;
        align-items: center;

        > div:first-child {
          font-family: $heroic;
          font-size: 80px;
          font-style: normal;
          font-stretch: condensed;
          line-height: 80px;
          letter-spacing: normal;
          text-align: center;
          color: #fff;

          margin-bottom: -10px; // correct for weird line height on font.
        }

        > div:last-child {
          text-align: center;
          font-size: 22px;
          line-height: 28px;
        }
        label {
          text-transform: uppercase;
          font-size: 22px;
          line-height: 28px;
          white-space: nowrap;
        }
        .broadcast-timer {
          margin: 0;
          align-self: flex-start;
        }
        .progressTimerText {
          color: #fff;
        }
        .progress {
          background-color: #fff;
        }
        .progressMeter {
          stroke: #fff;
        }
        .progressValue {
          stroke: #fff;
        }
      }
      line-height: 1.55;
      font-weight: lighter;

      .title {
        font-size: 45px;
        font-weight: bold;
        line-height: 115%;
      }

      .datetime {
        margin-top: 12px;
        font-size: 22px;
        font-weight: normal;
        text-transform: uppercase;
      }

      .potentialAchievementMetrics {
        margin-top: 32px;
        margin-bottom: 9px;
      }

      .description {
        margin-top: 15px;
        font-size: 20px;
      }
    }

    .tags {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 140px;
      box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.15);
      background: #fff;
    }
  }

  .plans {
    padding: 25px;
  }

  .planContent {
    width: 75vw;
  }

  .planLabel {
    color: #3d4756;
    font-size: 34px;
  }

  .planComments {
    display: flex;
    color: #3d4756;

    ul {
      flex: 1;

      li + li {
        margin-top: 10px;
      }
    }
  }

  .tabs {
    justify-content: center;
    border-bottom: transparent 4px solid;
    margin-top: 30px;
    a.nav-link {
      color: #434c5b88;
      padding: 16px 25px;
      text-transform: uppercase;
      font-size: 16px;
      font-weight: 500;
      &.active {
        color: #434c5b;
        border-bottom: #434c5b 4px solid;
      }
    }
  }
}

.playIcon {
  margin-right: 12px;
}

.devButton {
  padding: 0 1.5rem;
}
