.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  background: black;
  color: white;
}

.header {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.video {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.leaderboard {
  z-index: 1;
  position: fixed;
  top: 600px;
  right: 0;
  height: 800px;
}

.formFeedbacks {
  margin-bottom: 32px;
}

.footer {
  z-index: 9999;
  position: absolute;
  bottom: 0;
  width: 100%;

  padding: 0 48px;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}

.dashboard {
  position: relative;
  padding-bottom: 24px;
}
