.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(64px);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1500;
  padding-top: 236px;
}

.content {
  width: 740px;
}

.noHrm {
  padding: 24px;
  border-radius: 13px;
  background-color: var(--color-background-secondary);
}

.tooltipActive {
  opacity: 0.2;
}

.nextButton {
  position: fixed;
  bottom: 236px;
  left: 50%;
  transform: translateX(-50%);
}
