@import 'src/sass/fonts';
@import 'src/sass/colors';

.toast {
  z-index: 9999;
  position: absolute;
  top: 120px;
  left: 48px;
  right: 48px;

  padding-left: 40px;
  padding-right: 40px;
  height: 80px;
  color: white;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  border-radius: 10px;

  @include body-l-regular;
}

.success {
  background-color: $dark_blue;
}

.error {
  background-color: #dc3232;
}

.dismiss {
  @include body-uppercase;
  text-decoration-line: underline;
}

.hide {
  opacity: 0;
  transition: opacity 250ms linear;
}

.show {
  opacity: 1;
  transition: opacity 250ms linear;
}
