.zoneSegment {
  position: relative;
  width: 41.6px;
  margin-right: 4px;
}

.tickerBackground {
  position: absolute;
  width: 41.6px;
  background-color: var(--color-base-30);

  &.tickerBackgroundSmall {
    top: 66px;
    height: 12px;
  }

  &.tickerBackgroundLarge {
    top: 58px;
    height: 28px;
    border-radius: 4px;
  }
}

.firstZone {
  min-width: 8px;
  border-radius: 4px 0px 0px 4px;
}

.lastZone {
  border-radius: 0px 4px 4px 0px;
}

.zone1 {
  background-color: var(--color-base-70);
}

.zone2 {
  background-color: var(--color-light-blue);
}

.zone3 {
  background-color: var(--color-green);
}

.zone4 {
  background-color: var(--color-orange);
}

.zone5 {
  background-color: var(--color-red);
}

.zoneInactive {
  background-color: var(--color-base-30);
}

.zoneBoxSmall {
  position: absolute;
  height: 12px;
  margin: 66px 0px 28px 0px;
}

.zoneBoxLarge {
  position: absolute;
  height: 28px;
  margin: 58px 0px 18px 0px;
  border-radius: 4px;

  &.zoneBoxHighlight {
    &.zone1 {
      box-shadow: 0px 0px 201px rgba(196, 198, 204, 0.98), -11.78px 0px 92.9284px rgba(196, 198, 204, 0.726842),
        -0.84px 0px 19.4469px rgba(196, 198, 204, 0.450851), 1.97px 0px 10.8293px rgba(196, 198, 204, 0.365755),
        0.4px 0px 4.65761px rgba(196, 198, 204, 0.253158);
    }

    &.zone2 {
      box-shadow: 0px 0px 201px rgba(161, 205, 229, 0.98), -11.78px 0px 92.9284px rgba(161, 205, 229, 0.726842),
        -0.84px 0px 19.4469px rgba(161, 205, 229, 0.450851), 1.97px 0px 10.8293px rgba(161, 205, 229, 0.365755),
        0.4px 0px 4.65761px rgba(161, 205, 229, 0.253158);
    }

    &.zone3 {
      box-shadow: 0px 0px 201px rgba(126, 201, 127, 0.98), -11.78px 0px 92.9284px rgba(126, 201, 127, 0.726842),
        -0.84px 0px 19.4469px rgba(126, 201, 127, 0.450851), 1.97px 0px 10.8293px rgba(126, 201, 127, 0.365755),
        0.4px 0px 4.65761px rgba(126, 201, 127, 0.253158);
    }

    &.zone4 {
      box-shadow: 0px 0px 201px rgba(245, 129, 72, 0.98), -11.78px 0px 92.9284px rgba(245, 129, 72, 0.726842),
        -0.84px 0px 19.4469px rgba(245, 129, 72, 0.450851), 1.97px 0px 10.8293px rgba(245, 129, 72, 0.365755),
        0.4px 0px 4.65761px rgba(245, 129, 72, 0.253158);
    }

    &.zone5 {
      box-shadow: 0px 0px 201px rgba(225, 82, 71, 0.98), -11.78px 0px 92.9284px rgba(225, 82, 71, 0.726842),
        -0.84px 0px 19.4469px rgba(225, 82, 71, 0.450851), 1.97px 0px 10.8293px rgba(225, 82, 71, 0.365755),
        0.4px 0px 4.65761px rgba(225, 82, 71, 0.253158);
    }
  }
}
