@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  display: flex;

  background: #fff;
  padding: 20px 20px 0;
  border-bottom: 1px solid #9ea2bd;
}

.avatar {
  width: 40px;
  height: 40px;
  margin: 0 0 25px 0;
  &:first-child {
    margin-left: 0;
  }
}

.time {
  width: 108px;
  color: #3d457b;
  font-weight: 300;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.startingLabel {
  text-transform: uppercase;
  font-size: 22px;
  line-height: 28px;
  white-space: nowrap;
  align-self: flex-start;
}
.elapsedLabel {
  text-transform: uppercase;
  font-size: 22px;
  line-height: 28px;
  white-space: nowrap;
  align-self: center;
}

.timer {
  > div:first-child {
    font-family: $heroic;
    font-size: 80px;
    font-style: normal;
    font-stretch: condensed;
    line-height: 80px;
    letter-spacing: normal;
    text-align: center;
    color: #3d457b;

    margin-bottom: -10px; // correct for weird line height on font.
  }

  > div:last-child {
    text-align: center;
    font-size: 22px;
    line-height: 28px;
  }
}

.content {
  flex: 1;
  padding: 0;
}

.instructorImg {
  img {
    height: 100%;
  }
}

.date {
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 15px;
}

.contentSection {
  display: flex;
  position: relative;
  align-items: flex-end;
}

.instructorImg {
  width: 288px;
  height: 348px;
  overflow: hidden;

  img {
    width: 100%;
  }
}

.detail {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 25px 0 50px;
}

.title {
  font-size: 40px;
  line-height: 47px;
  font-weight: 500;
  margin-bottom: 25px;
  color: #3d4756;
}

.instructor {
  font-size: 22px;
  color: #3d457b;
  font-weight: normal;
  line-height: 28px;
  margin-bottom: 16px;
  text-transform: uppercase;
  display: flex;
}

.style {
  font-size: 22px;
  font-weight: normal;
  line-height: 28px;
  text-transform: uppercase;
  color: #9ea2bd;
  display: inline-flex;
  align-items: center;

  b {
    background-color: #9ea2bd;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 12px;
  }
}

.description {
  margin-top: 20px;
  font-size: 17px;
  font-weight: 300;
  color: #434c5b;
}

.participantsContainer {
  margin-bottom: 24px;
}

.expanded {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  margin-top: 50px;
  cursor: pointer;
}
