.container {
  display: inline-flex;
  width: 25%;
  color: #40477b;
  justify-content: center;

  &:before {
    display: none;
  }
}

.icon {
  margin-right: 15px;
}

.label {
  font-size: 20px;
  font-weight: normal;
  line-height: 1.67;
  color: #505d93;
}

.value {
  margin-top: 5px;
  font-size: 20px;
  text-transform: capitalize;
}
