@mixin sticky {
  position: sticky;
  z-index: 2;
  background: black;
}

.stickyTop {
  @include sticky;
  top: 0px;
}

.stickyBottom {
  @include sticky;
  bottom: 0px;
}

.oneUserCopy {
  position: absolute;
  top: 200px;
  margin: 0 20px;
  font-size: 22px;
  line-height: 28px;
  color: white;
}

%placeholder {
  display: inline-block;
  background-color: #4d4d4d;
  height: 12px;
}

.placeholderAvatar {
  background-color: #4d4d4d;
}

.placeholderRank {
  @extend %placeholder;
  width: 35px;
}

.placeholderUsername {
  @extend %placeholder;
  width: 176px;
}

.placeholderScore {
  @extend %placeholder;
  width: 55px;
}

.row {
  display: flex;
  align-items: center;
  height: 70px;
  padding: 17px 12px;
  border-bottom: 1px solid rgba(153, 159, 172, 0.2);

  &.isExpanded {
    height: 88px;
  }

  &.currentUserRow {
    background: rgba(42, 46, 55, 0.75);

    %placeholder {
      background-color: #d9d9d9;
    }

    .placeholderAvatar {
      background-color: #d9d9d9;
    }
  }

  .rank {
    margin-right: 10px;
    width: 58px;
    font-size: 24px;
    line-height: 28px;
    color: white;

    &.isExpanded {
      width: 60px;
      margin-right: 0px;
    }
  }

  .userName {
    margin-left: 16px;
    width: 192px;
    white-space: nowrap;
    overflow: hidden;
    font-size: 24px;
    line-height: 28px;
    color: white;
    text-decoration-line: underline;
  }

  .score {
    width: 84px;
    font-weight: normal;
    font-size: 24px;
    line-height: 28px;
    color: white;
    text-align: right;
  }
}

.moveUp {
  animation: slideUp 0.2s ease-out forwards;
}

.moveDown {
  animation: slideDown 0.2s ease-out forwards;
}

@keyframes slideUp {
  0% {
    opacity: 0.7;
    transform: translate(0px, 64px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes slideDown {
  0% {
    opacity: 0.7;
    transform: translate(0px, -64px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
