@import 'src/sass/fonts';
@import 'src/sass/colors';

$progress-container-height: 35px;
@mixin stacked-elements() {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  @include stacked-elements();
  width: 1080px;
  height: 1920px;
  padding: 0px 180px;
  font-family: $moderat;
  background-color: $black;
  color: $white;

  &.compact {
    .content {
      height: fit-content;
      margin-top: 405px;

      & > div {
        margin-top: auto;
      }
    }
  }

  &.compactCenter {
    .body {
      @include stacked-elements();
      justify-content: center;

      .title {
        margin-top: $progress-container-height * -1;
      }

      .content {
        height: fit-content;

        & > * {
          margin-top: auto;
        }
      }
    }
  }

  .progressContainer {
    height: $progress-container-height;
    width: 100%;
    margin-top: 20px;

    .progressHeader {
      font-size: 16px;
      text-transform: uppercase;
    }
    .progressBar {
      height: 5px;
      background-color: $dark_blue;
      margin-top: 8px;

      .progress {
        height: 100%;
        background-color: $white;
      }
    }
  }

  .body {
    height: 100%;
  }

  .title {
    text-align: center;
    font-size: 40px;
    margin-top: 145px;
  }

  .description {
    text-align: center;
    font-size: 26px;
    font-weight: 400;
    margin-top: 24px;
    color: $label-gray;
  }

  .content {
    @include stacked-elements();
    justify-content: space-between;
    height: 820px;
    margin-top: 94px;
  }
}
