.volumeControls {
  display: flex;
  align-items: center;

  .volumeDown {
    width: 54px;
    height: 54px;
    background-image: url(../../../../assets/icons/volume-down.svg);

    &:active {
      background-image: url(../../../../assets/icons/volume-down-active.svg);
    }
  }

  .volumeUp {
    width: 54px;
    height: 54px;
    background-image: url(../../../../assets/icons/volume-up.svg);

    &:active {
      background-image: url(../../../../assets/icons/volume-up-active.svg);
    }
  }

  .value {
    font-size: 24px;
    width: 56px;
    text-align: center;
  }
}

.volumeMix {
  width: 308px;
  height: 68px;
  border-radius: 6px;
  background-color: #16181f;
  margin-left: 26px;
  padding: 12px 36px 0 36px;
  position: relative;

  .tick {
    width: 1.4px;
    height: 20px;
    border-radius: 2px;
    background-color: #979797;
    position: absolute;
    left: 50%;
    top: 14px;
  }

  .labels {
    font-size: 13px;
    color: #ffffff;
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
  }
}
