.video-wrapper {
  position: relative;
  background: black;
  height: 100%;

  .video-overlay {
    position: absolute;

    left: 0;
    top: 0;
    right: 0;
    bottom: 0;

    background: rgba(0, 0, 0, 0.4);

    display: flex;

    flex-direction: column;

    justify-content: center;
    align-items: center;

    color: white;

    // video overlay is transparent to mouse clicks.
    pointer-events: none;

    > * {
      // however, child elements like buttons can receive clicks
      pointer-events: all;
    }

    &.pause {
      .pause-icon {
        // but the pause icon should not.
        pointer-events: none;
      }

      .pause-text {
        font-family: Moderat;
        font-size: 28px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: 1px;
        text-align: center;
        opacity: 0.6;
        margin-top: 1em;
        pointer-events: none;
      }
    }

    &.encore {
      background: #000;
    }
  }
}
