@import 'src/sass/colors';
$weight-drop-shadow-size: 1.75px;

.weights {
  position: relative;
  width: 36px;
  height: 36px;
  margin-bottom: 8px;
}

.weight {
  display: inline-block;
  width: 36px;
  height: 36px;
  border-radius: 100%;
  border: 9px solid white;
  animation-name: rest-icon-scale1, rest-icon-scale2;
  animation-delay: 0s, 333ms;
  animation-duration: 333ms, 0.6s;
  animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  position: relative;
  margin-right: -8px;
  margin-top: $weight-drop-shadow-size;

  @keyframes rest-icon-scale1 {
    0%,
    100% {
      transform: scale(0);
    }
  }

  @keyframes rest-icon-scale2 {
    0% {
      transform: scale(0);
    }
  }
}

.weightQuantity {
  color: white;
  position: absolute;
  background: #1c1d24;
  border: solid 1px #fff;
  border-radius: 4px;
  min-width: 25px;
  text-align: center;
  left: 50%;
  top: -8px;
  font-size: 14px;
}

.red {
  border-color: $pivot-red;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #ab2620, inset 0px 0px 0px $weight-drop-shadow-size #ab2620;
}

.blue {
  border-color: $pivot-light-blue;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #6b8098, inset 0px 0px 0px $weight-drop-shadow-size #6b8098;
}

.navy {
  border-color: $pivot-navy;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #1a1d46, inset 0px 0px 0px $weight-drop-shadow-size #1a1d46;
}

.yellow {
  border-color: $pivot-yellow;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #bbb734, inset 0px 0px 0px $weight-drop-shadow-size #bbb734;
}

.gray {
  border-color: #bababa;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #959595, inset 0px 0px 0px $weight-drop-shadow-size #959595;
}

.darkGray {
  border-color: #575757;
  box-shadow: 0px 0px 0px $weight-drop-shadow-size #3f3f3f, inset 0px 0px 0px $weight-drop-shadow-size #3f3f3f;
}
