.wrapper {
  position: relative;
  height: 22px;
  padding: 3px 0;
}

.ticker {
  position: absolute;
  top: 0;
  height: 22px;
  width: 3px;
  background-color: white;
  transition: 300ms;
  transition-timing-function: ease-out;
}

.progress {
  position: relative;
  height: 16px;
}

.background {
  height: 16px;
  width: 100%;
  background-color: white;
  opacity: 0.3;
}

.progressBar {
  position: absolute;
  top: 0;
  height: 16px;
  background-color: #fbfafa;
  transition: 300ms;
  transition-timing-function: ease-out;
}

.lowGlow {
  box-shadow: 0 0 2.25rem rgba(255, 255, 255, 0.5);
}

.mediumGlow {
  box-shadow: 0 0 2.25rem rgba(255, 255, 255, 0.75);
}

.fullGlow {
  box-shadow: 0 0 2.25rem rgba(255, 255, 255, 1);
}
