@import 'src/sass/fonts';
@import 'src/sass/colors';

.card {
  position: relative;
  font-family: $moderat;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px;
  border-radius: 10px;
  width: 720px;
  height: 193px;
  background-color: $white;
  color: $base-dark-grey;
  opacity: 0.6;

  &.summary {
    height: 156px;
  }

  .info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .summaryType {
      font-size: 16px;
      font-weight: 500;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    .goal {
      font-weight: 500;
      font-size: 26px;
      line-height: 40px;
    }

    .description {
      font-size: 16px;
      line-height: 28px;
      margin-top: 8px;
      text-align: start;
    }
  }
}

.selected {
  opacity: 1;
}
