.container {
  position: relative;
  width: 480px;
  height: 210px;

  display: flex;
  justify-content: center;
}

.title {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: white;
}

.gaugeContainer {
  position: absolute;
  top: -19px;
  left: 115px;
  height: 210px;
}

.pace {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);

  display: flex;
  flex-direction: column;
  align-items: center;

  .count {
    margin-bottom: 6px;
    font-family: 'Heroic';
    font-size: 60px;
    line-height: 72px;
    color: white;
    transition: color 200ms;
  }

  .countPaceTooFast {
    color: #f9423a;
  }

  .label {
    font-family: Moderat;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: white;
  }
}
