.container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .workoutCount {
    display: flex;
    align-items: center;
    font-family: Heroic;

    .count {
      font-size: 150px;
      font-weight: 350;
      font-stretch: condensed;
      color: #3d457b;
      margin: auto 36px;
      line-height: 176px;
    }

    .button {
      height: 78px;
      width: 78px;
      color: #3d457b;
      border-radius: 50%;
      border: 4px solid #3d457b;
      background-color: white;
      font-size: 40px;
      text-transform: uppercase;
      z-index: 1;

      &:focus {
        outline: none;
      }
    }
  }

  .recommended {
    padding: 8px 12px;
    font-size: 22px;
    background-color: #3d457b;
    color: white;
    border-radius: 8px;
    text-align: center;
    text-transform: uppercase;
    margin-top: -20px;
    &.hidden {
      visibility: hidden;
    }
  }
}
