@import '../../sass/colors.scss';

.workoutProgramDetail {
  display: flex;
  flex-direction: column;
  height: 100vh;
  line-height: normal;
  background: $light-bg;

  .loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  .classes {
    padding: 30px;
    overflow-y: auto;

    > div + div {
      margin-top: 15px;
    }

    .completedItem {
      img {
        filter: grayscale(100%);
        opacity: 0.5;
      }
    }
  }
}
