@import '../../sass/colors.scss';
@import '../../sass/fonts.scss';

.progressBoard {
  padding: 0;

  &.program-section.section {
    height: auto;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 #c8d8eb;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 22px;
    text-transform: uppercase;

    .title {
      font-size: 30px;
      color: #434c5b;

      span {
        font-weight: 400;
      }
    }

    .more {
      display: flex;
      align-items: center;
      font-size: 22px;
      font-weight: 500;
      color: #3d457b;
      cursor: pointer;

      &:after {
        content: ' ';
        display: inline-block;
        margin-left: 8px;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-left: 10px solid #3d457b;
      }
    }
  }

  .board {
    display: flex;
    align-items: flex-end;
    padding: 40px 30px 30px;
    margin: 0 30px;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 #c8d8eb;
    background-color: #fff;

    .rate {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-right: 66px;
      font-size: 20px;
      color: #f9423a;
      text-transform: uppercase;

      span {
        font-family: $heroic;
        font-size: 100px;
        font-weight: 200;
        line-height: 0.85;
      }
    }

    .progress {
      flex: 1;
      text-align: right;
      display: block;
      background: transparent;
      height: auto;

      label {
        display: inline-block;
        margin-top: 30px;
        margin-bottom: 10px;
        font-size: 22px;
        font-weight: 500;
        color: #434c5b;

        span {
          font-weight: normal;
        }
      }
    }
  }
}
