$circle-size: 40px;
$check-width: 18px;
$check-height: 13px;

.container {
  line-height: 0;

  .circle {
    position: relative;
  }

  .icon {
    position: absolute;

    // Center the check within the circle
    top: calc(($circle-size - $check-height) / 2);
    left: calc(($circle-size - $check-width) / 2);
  }
}
