@import 'src/sass/fonts';
@import 'src/sass/colors';

$faded-gray: rgba(255, 255, 255, 0.3);

.sequenceNumber {
  font-family: $heroic;
  font-size: 60px;
  line-height: 1;
  color: $faded-gray;

  &.selected {
    color: $white;
  }
}

.fadedText {
  // !important is needed to successfully override ClassItem styles with the same specificty
  color: $faded-gray !important;
}
