.zoneHighlight5 {
  position: relative;
}

.zoneHighlight4 {
  position: relative;
  top: 30px;
}

.zoneHighlight3 {
  position: relative;
  top: 60px;
}

.zoneHighlight2 {
  position: relative;
  top: 90px;
}

.zoneHighlight1 {
  position: relative;
  top: 120px;
}

.zoneBar {
  position: absolute;
  width: 200px;
  height: 30px;
  border-radius: 4px;
}

.zoneBar5 {
  background-color: var(--color-red);
}

.zoneBar4 {
  background-color: var(--color-orange);
}

.zoneBar3 {
  background-color: var(--color-green);
}

.zoneBar2 {
  background-color: var(--color-light-blue);
}

.zoneBar1 {
  background-color: var(--color-base-70);
}

.zoneHidden {
  opacity: 0;
}

.chart {
  position: absolute;
  width: 200px;
  height: 150px;
}
