.slider {
  width: 100vw;
  overflow-x: scroll;
  overflow-y: hidden;
}

.item {
  transition: all ease-in-out 0.2s;
  box-shadow: inset 0 0 transparent;
  outline: none;
  white-space: nowrap;

  &.selected {
    box-shadow: inset 0 -0.25rem var(--color-text-primary);
  }
}

.uppercase {
  text-transform: uppercase;
}
