.wrapper {
  color: #434c5b;
  text-align: center;
  border-radius: 10px;

  padding-top: 40px; // for text to fit better umnderneath the weight plates.

  margin: 30px;
  background-image: url('./join_a_program_no_text.png');
  height: 338px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  h1 {
    font-size: 45px;
    font-weight: medium;
    margin-bottom: 15px;
    display: block;
  }

  p {
    line-height: 135%;
    font-size: 22px;
    font-weight: normal;
    margin-bottom: 15px;
    width: 550px;
  }
}

// kilo overhaul
.boxShadow {
  box-shadow: 0 2px 4px #c8d8eb;
}
