.settingsPane {
  padding: 0 20px 0 20px;
  color: white;

  .score {
    display: flex;

    .points {
      margin-top: 30px;
      margin-right: auto;
      font-weight: 500;
      font-size: 22px;
      line-height: 28px;
    }

    .toggleGroup {
      margin-top: 24px;
      height: 20px;

      .toggleButton {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;

        outline: 0;
        box-shadow: none !important;
      }
    }
  }

  .divider {
    margin: 24px 0;
    height: 1px;
    opacity: 0.8;
    background: #4d4d4d;
  }

  .hintHeader {
    font-weight: 500;
    font-size: 22px;
    line-height: 28px;
  }

  .hintCopy {
    margin-top: 16px;
    font-size: 22px;
    line-height: 28px;
  }
}
