@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.avatar {
  object-fit: cover;
  border-radius: 50%;
  overflow: hidden;

  &.contain {
    object-fit: contain;
  }

  &.highlighted {
    animation: scale-up-10-percent 0.3s both;
  }

  &.unhighlighted {
    opacity: 0.5;
    transition: opacity 0.3s ease-out;
  }

  &.size-sm {
    width: 48px;
    height: 48px;
  }

  &.size-md {
    width: 64px;
    height: 64px;
  }

  &.size-lg {
    width: 96px;
    height: 96px;
  }
}

.userInitials {
  background: $pivot-light-blue;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $moderat;
  text-transform: uppercase;
  color: $dark;
  border-radius: 50%;

  // Smaller initials for the Select User page.
  &.smallInitials {
    font-size: 2.5em;
  }

  &.coloredStyle0 {
    background: #6977d4;
    color: white;
  }
  &.coloredStyle1 {
    background: #f9423a;
    color: white;
  }
  &.coloredStyle2 {
    background: #c8d8eb;
  }
  &.coloredStyle3 {
    background: #e4e179;
  }
}

@keyframes scale-up-10-percent {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}
