@import 'src/sass/fonts';

.content {
  padding: 25px 25px 80px;
  width: 85vw;

  .row {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .device {
    height: 395px;
  }

  .deviceWrapper {
    position: relative;
    width: 100%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: -90px;
  }

  .area {
    background: #bdc9da;
    width: 550px;
    height: 550px;
    transform: rotateX(130deg) translateZ(106px);
    margin: 0 auto;
    transform-style: preserve-3d;
  }

  // .area states
  .ready {
    background: #aecda0;
  }

  .idle {
    background: #bdc9da;
  }

  .area.actual {
    transform: none;
    width: 22vw;
    height: 22vw;
  }

  .positionWrapper {
    height: 640px;
    perspective: 400px;
    perspective-origin: center top;
    position: absolute;
    width: calc(100% - 50px);
    bottom: 0;
  }

  .mat {
    $width: 36%;
    width: $width;
    height: 22%;
    background: #fff;
    opacity: 0.5;
    position: absolute;
    z-index: 2;
    left: calc(50% - #{$width}/ 2);
    bottom: 10%;
  }

  .arrow {
    width: 16%;
    height: 28%;
    margin-left: -8%;
    margin-top: -4%;
    opacity: 1;
    position: absolute;
    perspective: none;
    transform-style: preserve-3d;
    transform: rotateX(-130deg) translate(0, -38px);
    z-index: 5;
    transition: all 0.2s;
  }
}
