@import 'src/sass/colors.scss';

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 127px;
  width: 100%;
  background-color: $white;
  color: $black;
  font-size: 16px;

  svg {
    fill: $black;
  }

  &.darkTheme {
    background-color: $black;
    color: $white;
    svg {
      fill: $white;
    }
  }

  .tempoLogo {
    width: 149px;
    text-align: center;

    h2 {
      font-size: 36px;
      font-weight: 500;
    }
  }
}
