@import 'src/sass/colors';

.milestone {
  margin: 20px 15px;
  width: 160px;
  text-align: center;
  position: relative;

  img {
    width: 92px;
    height: 92px;
  }

  .headline {
    margin: 16px 0 10px 0;
    font-size: 20px;
    line-height: 1.1;
    color: $dark_blue;
  }

  .description {
    font-size: 20px;
    opacity: 0.6;
    color: $dark_blue;
    line-height: 1.1;
  }

  .weight {
    position: absolute;
    top: 8%;
    font-size: 34px;
    color: white;
    width: 100%;

    small {
      font-size: 14px;
      display: block;
      text-transform: uppercase;
      margin-top: -10px;
    }
  }
}
