@import 'src/sass/colors';

.box {
  width: 345px;
  height: 356px;
  background-color: $white;
  color: $base-dark-grey;
  border-radius: 10px;
  padding: 56px 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  .image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 104px;
    width: 104px;
  }

  .details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    h1 {
      font-weight: 500;
      font-size: 26px;
      line-height: 32px;
      margin: 0;
    }

    h2 {
      font-weight: normal;
      font-size: 16px;
      line-height: 22px;
      margin: 0;
    }
  }
}
