.marquee {
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  .slider {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
    left: 0;
  }

  &.on {
    .slider {
      display: inline-block;
      overflow: visible;
      transition: all linear 15s;
    }
  }
  &.off .slider {
    left: 0 !important;
    position: static;
  }
}
