@import 'src/sass/colors.scss';

.records {
  display: flex;
  border-radius: 10px;
  background-color: white;
  justify-content: center;
  padding: 35px;
  margin-bottom: 30px;

  .record {
    margin: 0 40px;
    text-align: center;
    position: relative;

    img {
      height: 70px;
      width: 70px;
    }

    .weight {
      position: absolute;
      font-size: 25px;
      color: white;
      top: 15px;
      width: 100%;

      div {
        text-transform: uppercase;
        font-size: 10px;
        margin-top: -8px;
        font-weight: 300;
      }
    }

    .title {
      color: $dark_blue;
      font-size: 17px;
      line-height: 1.29;
      font-weight: 500;
      margin-top: 12px;
    }

    .data {
      color: $dark_blue;
      font-size: 17px;
      line-height: 1.29;
      opacity: 0.6;
    }
  }
}
