.page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.navigation {
  height: 99px;
  padding-top: 60px;
  padding-left: 50px;
  background: #fff;
  box-shadow: 0px 7px 13px transparentize($color: #3d457b, $amount: 0.66);
  z-index: 9;

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    height: 90px;
    background: linear-gradient(to right, rgba(#fff, 0) 80%, #fff 100%);
    left: 0;
    right: 0;
    z-index: 10;
    pointer-events: none;
  }
}

.select {
  position: fixed;
}
