@import 'src/sass/colors';

.container {
  margin: 30px 30px;

  .user {
    margin-bottom: 20px;
  }

  .adviceContainer {
    margin-top: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 40px;
    color: $dark;
    opacity: 0.5;
    font-weight: 400;

    .adviceText {
      width: 55%;
      text-align: center;
      line-height: 1.2;
    }
  }
}

.buttonsContainer {
  position: fixed;
  top: 20px;
  right: 30px;
  z-index: 10;
  border: solid 1px #3d457b;
  border-radius: 10px;
  display: flex;

  .mode {
    display: flex;
    font-size: 16px;
    color: #3d457b;
    text-transform: uppercase;
    width: 100px;
    text-align: center;
    padding: 8px 0;
    justify-content: center;
    align-items: center;

    &.active {
      color: white;
      background-color: #3d457b;
      border-radius: 8px;
      border: solid 1px #3d457b;
    }
  }
}
