.overlay {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 75px;
  border-radius: 4px;

  .label {
    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-feature-settings: 'pnum' on, 'lnum' on;
    &::before {
      content: '\00a0';
    }
  }
  .completed {
    color: rgba(126, 191, 56, 1);
  }
  .missed {
    color: rgba(169, 169, 169, 1);
  }
}
