@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.wrapper {
  width: 100%;
  background-color: $light-bg;
  font-family: $moderat;
  font-style: normal;
  display: flex;
  flex-direction: column;
  padding: 40px 38px 5px;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-transform: uppercase;

    .title {
      background: #c8d8eb;
      border-radius: 56px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: $dark_blue;
      padding: 8px 22px;
      margin-bottom: 0.5rem;
    }
    .seeAll {
      color: $dark;
      font-size: 20px;
      line-height: 28px;
      text-decoration-line: underline;
    }
  }

  .classes {
    margin-top: 8px;
    display: flex;
    justify-content: flex-start;

    & > div:not(:last-child) {
      margin-right: 24px;
    }
  }
}
