@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 50px 32px 90px 32px;
  color: $dark_blue;

  .preferences {
    font-size: 22px;
    margin-bottom: 24px;

    a {
      color: $dark_blue;
      font-weight: 500;
      text-decoration: underline;
    }
  }

  .workoutProgram {
    margin-bottom: 48px;
  }
}
