.header {
  display: flex;
  height: 80px;
  padding: 16px 12px;

  .groups,
  .settings {
    padding-top: 4px;
  }

  .settings {
    svg {
      height: 40px;
      width: 40px;
    }
  }

  .minimize {
    margin-left: auto;
    height: 48px;
    width: 48px;
    padding: 4px 0 0 4px;

    svg {
      height: 40px;
      width: 40px;
    }
  }
}
