@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  font-family: $moderat;
  color: $dark_blue;

  .title {
    align-self: flex-start;
    font-size: 32px;
    font-weight: 500;
    margin: 32px 0px 8px 32px;
  }

  .subtitle {
    align-self: flex-start;
    width: 860px;
    font-size: 26px;
    margin-left: 32px;
    margin-bottom: 55px;
  }

  .safetyVideoSection {
    margin-top: 55px;
    width: 100%;
  }

  .qrCodeSection {
    margin-top: 55px;
    display: flex;
    align-items: center;
    height: 210px;
    background-color: $white;
  }

  .section {
    margin-top: 55px;
    width: 100%;
  }
}
