@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.groupItem {
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
  flex-direction: row;
  color: $dark_blue;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.groupTitleDescription {
  display: flex;
  flex-direction: column;
  min-width: 0;
  margin-right: 30px;
}

.groupTitleRow {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.groupTitle {
  font-family: $moderat;
  font-size: 32px;
  line-height: 40px;
  font-weight: 500;
  margin-right: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  min-width: 0;
}

.groupMemberCount {
  @include body-l-regular;
  white-space: nowrap;
}

.groupDescription {
  @include body-regular;
  margin-top: 8px;
}

.button {
  flex-shrink: 0;
  text-transform: uppercase;
}
