.page {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;

  .button {
    background-color: var(--color-white);
    color: var(--color-black);
    position: fixed;
    bottom: 17.25rem;
    left: 50%;
    transform: translateX(-50%);
  }
}

.calories {
  background: radial-gradient(94.56% 94.56% at 50% 34.17%, rgba(224, 81, 71, 0.5) 0%, rgba(224, 81, 71, 0) 100%), black;
}

.default {
  background: radial-gradient(92.27% 92.27% at 50% 33.93%, rgba(153, 159, 172, 0.5) 0%, rgba(153, 159, 172, 0) 100%),
    black;
}

.intensity {
  background: radial-gradient(91.64% 91.64% at 50% 33.26%, rgba(245, 129, 72, 0.5) 0%, rgba(245, 129, 72, 0) 100%),
    black;
}

.strength {
  background: radial-gradient(92.34% 92.34% at 50% 34.04%, rgba(51, 87, 222, 0.4) 0%, rgba(51, 87, 222, 0) 100%), black;
}
