@import '../../sass/fonts';
@import '../../sass/sizes';

$violetColor: #3d457b;

.nav {
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: $top-nav-size;
  padding: 0 35px;
  background: #fff;
  box-sizing: border-box;
  z-index: 3;
  transition: all ease-in-out 0.3s;
  position: relative;

  .back {
    margin-left: -1rem;
  }

  &.transparent {
    background: transparent;
  }

  .title {
    font-size: 29px;
    color: $violetColor;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    position: absolute;
    width: 100%;
    left: 0;
    z-index: -1;
  }
}

.nav.fixedNav {
  box-shadow: 0 -7px 13px 0 rgba(61, 69, 123, 0.15);
  background-image: linear-gradient(95deg, #707bb2, #3d457b);

  .title {
    color: #fff;
  }

  .back {
    color: #fff;
  }
}

.buttonOnly {
  justify-content: flex-end;
}

// kilo overhaul
.kiloNav {
  position: relative;
  flex-shrink: 0;
  z-index: 3;
}

.kiloTitle {
  position: absolute;
  z-index: -1;
  width: 100%;
  left: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.negativeMarginLeft {
  margin-left: -1rem;
}
