.row {
  display: flex;
  font-weight: 400;
  font-size: 22px;
  line-height: 28px;
  justify-content: space-between;
  margin-top: 24px;
  margin-right: auto;

  &.active {
    font-weight: 700;
  }
}

.title {
  flex-grow: 4;
}

.count {
  flex-grow: 1;
  text-align: end;
}

.divider {
  margin: 24px 0;
  height: 1px;
  opacity: 0.8;
  background: #4d4d4d;

  &:last-child {
    visibility: hidden;
  }
}
