.dots {
  display: block;

  padding: 0;
  margin: 0;
  width: 100%;
}

.dot {
  display: inline-block;

  margin: 10px 6px 5px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  cursor: pointer;

  opacity: 0.3;
  background-color: black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.selected {
  opacity: 1 !important;
}
