@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: $moderat;
  text-align: center;
  color: $white;
  position: relative;
  height: 940px;
  width: 1080px;

  .image {
    position: absolute;
    margin-top: 92px;
  }

  .title {
    margin-top: 291px;
    width: 629px;
    font-size: 40px;
    line-height: 47px;
    font-weight: 500;
    z-index: 2;
  }

  .subtitle {
    font-weight: normal;
    font-size: 22px;
    line-height: 28px;
    margin-top: 31px;
    z-index: 2;
  }

  .button {
    position: relative;
  }
}
