.container {
  position: relative;

  width: 228px;
  height: 210px;
}

.heading {
  display: flex;

  margin-bottom: 12px;
}

.title {
  font-size: 28px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;

  color: #ffffff;
  opacity: 0.6;

  margin-bottom: 0;
}

.subtitle {
  flex: 1;
  text-align: right;

  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
  text-transform: uppercase;

  color: #ffffff;
  opacity: 0.6;
}

.lineChart {
  position: absolute;
  top: 44px;
  left: 20px;
  right: 0;
  bottom: 0;
}
