@import 'src/sass/colors';
@import 'src/sass/fonts';

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $dark_blue;
  font-family: $moderat;
  width: 700px;
  margin: auto;
  text-align: center;

  h1 {
    margin-top: 70px;
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
    font-style: normal;
  }

  h2 {
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 64px;
    color: $dark_blue;
  }

  .info {
    display: flex;

    > :not(:last-child) {
      margin-right: 16px;
    }

    margin-bottom: 48px;
  }
}
