@import 'src/sass/colors';
@import 'src/sass/fonts';

.wrapper {
  width: 228px;
  height: 210px;

  color: $white;
}

.title {
  opacity: 0.6;
  text-transform: uppercase;
  font-family: $moderat;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: 1px;
}

.repCount {
  display: flex;
  align-items: baseline;
  margin-top: 32px;

  height: 80px;
}

.reps {
  font-family: $heroic;
  font-size: 80px;
  line-height: 80px;
}

.targetReps {
  margin-left: 16px;
  opacity: 0.6;
  font-family: $heroic;
  font-size: 60px;
}

.lastSet {
  margin-top: 8px;
  opacity: 0.6;
  font-family: $moderat;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
}
