@import 'src/sass/fonts';
@import 'src/sass/colors';

.item {
  margin: 0 30px;
  padding: 37px 0;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(67, 76, 93, 0.25);
  }

  display: flex;
  align-items: center;
  justify-content: space-between;

  font-family: Moderat;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 36px;

  color: #434c5b;
}
