@import 'src/sass/fonts';

.classSessionViewerActions {
  height: 96px;
  background-color: #4d526288;
  margin-top: -104px;
  transition: all ease-in-out 0.2s;
  padding: 0 48px;
  z-index: 800;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .exit {
    display: flex;

    .exitLabel {
      @include body-l-uppercase;
      color: #ffffff;
      margin-left: 26px;
      line-height: unset;
    }
  }

  .classSessionInfo {
    margin-left: 45px;
    display: flex;
    flex-direction: row-reverse;

    .userImg img {
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background: #ffffff;
      margin-left: 24px;
    }
  }

  &.expanded {
    margin-top: 0;
  }
}
