@import 'src/sass/colors.scss';

.class-group {
  $padding: 30px;

  &--section {
    background-color: $light-bg;
    margin-bottom: 30px;
  }

  &--titleBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    > h3 {
      color: #434c5b;
      font-size: 16px;
      font-weight: 500;
      background-color: #c8d8eb;
      display: inline-block;
      padding: 9px 22px;
      border-radius: 18px;
      text-transform: uppercase;
    }

    .buttonsWrapper {
      height: 36px;
      padding: 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: Helvetica;
      font-size: 16px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;

      span {
        border: solid 1px #3d457b;
      }
      span:nth-child(1) {
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
      }
      span:nth-child(2) {
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
      }
      .changeModeButton {
        padding: 8px 24px;
        color: #3d457b;
        background-color: rgba(61, 69, 123, 0);
        &.active {
          background-color: #3d457b;
          color: #ffffff;
        }
      }
    }
  }
}
