$moderat: Moderat;
$heroic: Heroic;

$font-body: $moderat;
$font-headers: $moderat;
$font-numbers: $heroic;

@mixin headling-medium {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  font-size: 40px;
  line-height: 47px;
}

@mixin body-sm-regular {
  font-family: $moderat;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 28px;
}

@mixin body-sm-uppercase {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
}

@mixin body-regular {
  font-family: $moderat;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 28px;
}

@mixin body-uppercase {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
  text-transform: uppercase;
}

@mixin body-l-regular {
  font-family: $moderat;
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 32px;
}

@mixin body-l-uppercase {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  font-size: 26px;
  line-height: 32px;
  text-transform: uppercase;
}

@mixin h4-regular {
  font-family: $moderat;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 36px;
}

@mixin h4-uppercase {
  @include h4-regular;
  text-transform: uppercase;
}
