.workout-plan {
  display: flex;
  align-items: center;
  justify-content: space-between;

  background: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0px rgba(104, 114, 169, 0.2);
  cursor: pointer;
  position: relative;
  flex-direction: column;

  & + & {
    margin-top: 25px;
  }

  & > .info:after {
    content: ' ';
    background: #f5f5f8;
    border-radius: 8px;
    width: 430px;
    height: 96px;
    display: block;
    position: absolute;
    right: 50px;
    z-index: 1;
  }

  & > .info {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px 10px 0 0;
    padding: 25px 50px;
    background: #fff;
    cursor: pointer;
    position: relative;
    border-bottom: solid 1px #434c5b33;
  }

  & > .table-panel {
    padding: 5px 50px 25px 50px;
    width: 100%;
  }

  .table {
    thead {
      th {
        font-size: 22px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #434c5b;
        text-transform: uppercase;
        border-top: none;
        padding: 40px 0.75rem;
        text-align: center;
        vertical-align: middle;

        &:first-child {
          padding-left: 0;
        }
      }
    }

    tbody {
      td {
        font-size: 22px;
        font-weight: 300;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #434c5b;
        padding: 30px 0.75rem;
        vertical-align: middle;
        text-align: center;

        & > .circle {
          display: block;
          margin: 0 auto 5px auto;
        }

        &:first-child {
          padding-left: 0;
        }
      }

      .detail {
        & > td {
          padding: 0 0 30px 0;
          border-top: none;
        }
        & + tr {
          .trend {
            display: none;
          }
        }
      }
    }
  }

  .label {
    display: flex;
    align-items: center;
    color: #3d4756;
    font-size: 34px;
    position: relative;
    z-index: 2;
    max-width: 50%;
    padding-right: 75px;
  }

  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    margin-left: 15px;
    color: #f9413a;
    border-radius: 30px;
    border: 2px solid #f9413a;
    position: absolute;
    right: 0;
  }

  .images {
    width: 430px;
    text-align: center;
    height: 179px;

    .image {
      height: 100%;
      position: relative;
      z-index: 2;
    }
  }

  .set-feedback {
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;

    .circle {
      margin-right: 10px;
    }

    .expand {
      margin-left: 14px;
    }
  }

  .progressbar {
    &.thick {
      height: 10px;
    }

    &.naked {
      background: transparent;
    }
  }
}
