$tab-bar-height: 87px;

.tabMenu {
  display: flex;
  height: $tab-bar-height;
  background-color: #fff;
  box-shadow: 0 -4px 32px 0 rgba(93, 121, 154, 0.6);
  z-index: 100;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  .overlay {
    position: fixed;
    width: 100vw;
    height: calc(100vh - #{$tab-bar-height});
    background: rgba(0, 0, 0, 0.4);
    top: 0;
    left: 0;
  }

  .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: stretch;

    svg {
      margin-bottom: 6px;
    }

    .username {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .avatarIcon {
      width: 44px;
      height: 44px;
      margin-bottom: 6px;
      border-radius: 50%;

      font-size: 22px;
      font-weight: 100;
      flex-shrink: 0;
    }
  }
}

.usersMenu :global {
  .popover {
    border: 0px solid !important;
    margin-right: 50px;
    margin-bottom: 50px !important;
    z-index: 1040;
    width: 500px !important;
    max-width: 500px !important;

    .popover-inner {
      overflow: hidden;
    }

    .arrow {
      bottom: calc((1rem + 1px) * -1) !important;
      width: 2rem !important;
      height: 1rem !important;

      &::before,
      &::after {
        border-width: 1rem 1rem 0 !important;
      }
    }
  }
}
