.container {
  width: 100%;
}

.formFeedback {
  position: relative;

  display: inline-flex;
  flex-direction: column;
  align-items: center;

  margin-right: 24px;

  transition: opacity 0.6s;
  opacity: 0;
  animation: fadeIn 0.6s linear forwards;

  &.hiddenFeedback {
    opacity: 1;
    animation: fadeOut 0.6s linear forwards;
  }
}

.icon {
  width: 64px;
  height: 64px;

  margin-bottom: 16px;
}

.title {
  margin-bottom: 8px;
  border-radius: 2px;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 8px;
  padding-right: 8px;
  min-width: 124px;
  text-align: center;

  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  text-transform: capitalize;

  color: white;
  background: rgba(249, 66, 58, 0.6);

  &.correctedTitle {
    background: rgba(184, 233, 134, 0.5);
    color: white;
  }
}

.correctedLabel {
  color: #88ca3e;
  font-size: 16px;

  &.hidden {
    opacity: 0;
  }
}

.checkmark {
  position: absolute;
  top: 42px;
  right: 0;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
