@import 'src/sass/fonts';
@import 'src/sass/colors';

.message {
  position: absolute;
  z-index: 10;
  font-family: $moderat;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  color: $white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 248px;
  width: 491px;
  margin-top: 179px;

  .title {
    font-weight: 500;
    font-size: 40px;
    line-height: 47px;
  }

  .subtitle {
    font-size: 22px;
    line-height: 28px;
  }
}
