@import 'src/sass/colors';

.textInput {
  position: relative;
  width: 100%;
  margin-bottom: 18px;
  line-height: normal;

  &.readOnly input {
    background: rgba(200, 216, 235, 0.3);
  }

  &.error {
    margin-bottom: 14px;
  }

  input {
    width: 100%;
    padding: 51px 24px 23px;
    font-size: 30px;
    font-style: normal;
    color: #3d457b;
    border-radius: 10px;
    border: 2px solid #c8d8eb;
    box-shadow: 0 12px 14px 0 rgba(200, 216, 235, 0.3);
    background-color: rgba(255, 255, 255, 0.7);
    outline: none;
    box-sizing: border-box;

    &.darkTheme {
      background-color: $dark_blue;
      color: $white;
      border: none;
      box-shadow: none;
    }

    &:focus {
      border-color: #3d457b;
    }

    &::placeholder {
      color: transparent;
    }

    &:focus::placeholder {
      color: rgba(61, 69, 123, 0.3);
      border-color: #3d457b;
    }

    & + label {
      transition: 0.2s ease all;
      position: absolute;
      left: 28px;
      top: 38px;
      font-size: 30px;
      font-weight: normal;
      pointer-events: none;
    }

    &:focus,
    &:not(:placeholder-shown) {
      & + label {
        top: 17px;
        font-size: 25px;
        font-weight: 300;
        color: #3d457b;
      }
    }
  }

  label {
    color: rgba(61, 69, 123, 0.3);
    &.darkTheme {
      color: $white !important;
    }
  }

  .errorMessage {
    margin: 7px 0 0 28px;
    font-size: 25px;
    font-weight: 300;
    color: #ff0000;
  }

  .errorIcon {
    width: 32px;
    height: 28px;
    background: url(../../assets/icons/input-error.svg);
    position: absolute;
    right: 31px;
    top: 40px;
  }
}
