@import 'src/sass/colors';

.footer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: fixed;
  bottom: 0;
  height: 238px;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.23) 100%);

  button {
    height: 77px;
    width: fit-content;
    padding: 22px 55px;
    text-align: center;
    font-size: 26px;
    line-height: 30px;
    font-weight: 500;
    text-transform: uppercase;
    color: $white;
    background-color: transparent;
    border: $white solid 3px;
    border-radius: 12px;
    margin-bottom: 32px;
  }
}
