@import 'src/sass/colors';
@import 'src/sass/fonts';

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  height: 264px;
  width: 720px;
  padding: 32px;
  font-family: $moderat;
  border-radius: 10px;
  opacity: 0.6;
  background-color: white;
  color: $base-dark-grey;
  overflow: hidden;
  z-index: 10;

  &.selected {
    opacity: 1;

    &:not(.summary) {
      border: 3px solid $white;
    }
  }

  &.summary {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 178px;

    .content {
      width: 482px;
      margin: 0px;
      .header {
        margin: 8px 0px;
      }
      .longDescription {
        padding: 0px;
        margin-top: 0px;
        border: none;
      }
    }
  }

  img {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -5;
  }

  .content {
    width: 304px;
    height: fit-content;
    text-align: left;

    .experienceType {
      font-size: 16px;
      font-weight: 500;
      text-transform: uppercase;
    }

    .header {
      font-size: 26px;
      margin-bottom: 16px;
    }

    .longDescription {
      padding-bottom: 16px;
      border-bottom: 1px solid;
      margin-bottom: 16px;
    }
  }
}
