@import 'src/sass/colors';

.wrapper {
  height: 940px;
  width: 1080px;
  background-color: $black;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  .classSource {
    z-index: 2;
    margin-top: 19px;
    background-color: $hero-error;
    height: 18px;
    width: 187px;
  }

  .classSourceDetail {
    z-index: 2;
    margin-top: 20px;
    background-color: $hero-error;
    height: 28px;
    width: 267px;
  }

  .image {
    margin-top: 7px;
    position: absolute;
  }

  .variant {
    z-index: 2;
    background-color: $hero-error;
    height: 28px;
    width: 121px;
    margin-top: 443px;
  }

  .heroTitle {
    z-index: 2;
    background-color: $hero-error;
    height: 36px;
    width: 402px;
    margin-top: 36px;
  }

  .heroSubtitle {
    z-index: 2;
    background-color: $hero-error;
    height: 18px;
    width: 271px;
    margin-top: 24px;
  }

  .heroButtons {
    margin-top: 26px;
    display: flex;
    justify-content: space-between;

    .heroButton {
      height: 64px;
      width: 234px;
      background-color: $hero-error;
      margin: 0 12px;
    }
  }

  .details {
    margin-top: 28px;
    display: flex;
    justify-content: space-between;
    width: 290px;
    height: 36px;

    .detail {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      margin: 0 15px;

      .detailLabel {
        background-color: $hero-error;
        height: 12px;
        width: 79px;
      }

      .detailValue {
        background-color: $hero-error;
        height: 12px;
        width: 137px;
      }
    }
  }
}
