@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  position: relative;
  width: 396px;
  max-height: 860px;
  background-color: $panel-gray;
  opacity: 0.9;
  box-shadow: 0px 0px 0px 4px rgba(255, 255, 255, 0.35);
  border-radius: 8px;
  overflow: scroll;
  backdrop-filter: blur(10px);
}

.expanded {
  animation: expand-height 0.3s;
  animation-timing-function: cubic-bezier(0.06, 0.59, 0.66, 0.98);
  @keyframes expand-height {
    0% {
      max-height: 0px;
    }
    100% {
      max-height: 860px;
    }
  }
}
