@import 'src/sass/fonts';

.container {
  margin: 50px 20px 30px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  min-height: 500px;
  max-height: 700px;
  width: 798px;
  text-align: center;

  .header {
    margin: 50px 0 24px;
    font-family: $moderat;
    font-size: 40px;
    font-weight: 500;
    line-height: 47px;
    color: #434c5b;
  }

  .subtitle {
    font-family: $moderat;
    font-size: 26px;
    line-height: 32px;
    color: #434c5b;
    margin-bottom: 48px;
  }

  .radioGroup {
    max-height: 482px;
    overflow-y: scroll;

    .divider:last-of-type {
      visibility: hidden;
    }
  }

  .radio {
    display: flex;
    text-align: left;
    width: 798px;
    margin-left: 92px;

    .radioInput {
      margin: 16px -15px 0px 0px;
    }

    .radioInputLabel {
      display: flex;
      width: 100%;
    }

    .device {
      font-family: $moderat;
      font-size: 30px;
      line-height: 36px;
      color: #3d457b;
      margin-bottom: 8px;
    }

    .serialNumber {
      font-family: $moderat;
      font-size: 18px;
      line-height: 21px;
      color: #3d457b;
    }

    .bpm {
      margin: 10px 62px 0 auto;
      font-family: $moderat;
      font-size: 30px;
      color: #3d457b;
    }
  }

  .divider {
    margin: 24px 0px 24px 50px;
    height: 1px;
    width: 798px;
    background-color: #434c5d;
    opacity: 0.25;
  }
}

.back {
  margin-right: auto;
  padding: 0;
}
