.page {
  position: fixed;
  top: 0;
  left: 0;
  background: radial-gradient(92.27% 92.27% at 50% 33.93%, rgba(153, 159, 172, 0.5) 0%, rgba(153, 159, 172, 0) 100%),
    black;
  z-index: 1500;

  .card {
    border-radius: 13px;
    background-color: #e9e7e5;
    overflow: hidden;

    .image {
      position: absolute;
    }
  }

  .confetti {
    position: absolute;
    pointer-events: none;
    right: 0;
    top: 0;
  }
}
