@import 'src/sass/fonts';
@import 'src/sass/colors';

.content {
  font-family: $moderat;
  padding: 35px 9px 50px 9px;

  .text {
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 32px;
    text-align: center;
    color: $dark_blue;
  }

  .header {
    font-weight: 500;
    font-size: 40px;
    line-height: 47px;
    padding-bottom: 17px;
  }

  .details {
    margin-top: 40px;
    display: flex;
    justify-content: center;

    .phones {
      position: relative;
      width: 336px;
      height: 580px;
      display: flex;
      margin-left: 21px;

      .iosPhone {
        height: 509px;
      }

      .androidPhone {
        height: 535px;
        position: absolute;
        top: 44px;
        left: 84px;
      }
    }

    .rightCol {
      margin-left: 48px;
      margin-top: 69px;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 312px;
      :not(:first-child) {
        margin-top: 40px;
      }

      .circle {
        width: 60px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 60px;

        .or {
          text-transform: uppercase;
          font-style: normal;
          font-weight: 500;
          font-size: 26px;
          line-height: 31px;
          color: $label-gray;
          position: relative;
          top: 1px;
        }
      }
    }
  }
}
