@import 'src/sass/colors';
@import 'src/sass/fonts';

.wrapper {
  position: relative;

  width: 312px;
  height: 210px;

  color: $white;
}

.title {
  position: absolute;
  text-transform: uppercase;
  font-family: $moderat;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: 1px;
}

.repCount {
  display: flex;
  padding: 8px 0px 0px 120px;
}

.reps {
  width: 96px;
  text-align: right;
  font-family: $heroic;
  font-size: 140px;
  line-height: 123px;
}

.repsHighlight {
  text-shadow: 0px 0px 36px white;
}

.targetReps {
  height: 72px;
  margin: 50px 0px 0px 24px;
  opacity: 0.6;
  font-family: $heroic;
  font-size: 60px;
  line-height: 70px;
}

.lastSet {
  opacity: 0.6;
  font-family: $moderat;
  font-size: 28px;
  line-height: 33px;
  text-transform: uppercase;
}

.lastSetWithTarget {
  margin-top: 22px;
}

.lastSetNoTarget {
  margin-top: 9px;
}
