@import 'src/sass/fonts';
@import 'src/sass/colors';

.wrapper {
  width: 670px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .message {
    @include headling-medium;
    color: $dark_blue;
    text-align: center;
    margin-bottom: 32px;
  }

  .subMessage {
    @include body-regular;
    color: $dark_blue;
    text-align: center;
    margin-top: -16px;
    margin-bottom: 32px;
  }

  .icon {
    width: 60px;
    height: 60px;
    margin-bottom: 32px;
  }
}
