.leaderboard {
  font-family: Moderat;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 23px !important;
  height: 213px;
  .leaderboardItem {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 14px;

    .rank {
      width: 18px;
      height: 28px;
      font-family: Moderat;
      font-size: 24px;
      font-weight: 200;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #434c5b;
      margin-right: 11px;
    }

    .avatarIcon {
      width: 50px;
      height: 50px;
      max-width: 50px;
      max-height: 50px;
      font-size: 25px;
      font-weight: 100;
      margin-right: 19px;
    }

    .leaderDetails {
      color: #434c5b;
      .username {
        font-size: 20px;
        text-decoration: underline;
      }
      .score {
        font-size: 18px;
        .unit {
          font-size: 16px;
          margin-left: 4px;
          color: rgba(61, 71, 86, 0.64);
        }
      }
    }
  }
}
