@import 'src/sass/fonts';
@import 'src/sass/colors';

.content {
  overflow: hidden;
}

.flexGrow {
  flex-grow: 1;
}

.verticalRightDivider {
  border-right: 1px solid var(--color-base-60);
}

.progressBar {
  height: 4px;
  background: #f3f3f3;
  border-radius: 10px;
  margin-top: 12px;
}

.progress {
  height: 100%;
  border-radius: 10px;
  background-color: var(--color-base-50);

  &.current {
    background-color: var(--color-base-20);
  }
}
