@import 'src/sass/colors';
@import 'src/sass/fonts';

.wrapper {
  width: 480px;
  height: 210px;

  color: $white;

  opacity: 1;
  transition: opacity 250ms;
}

.fadedOut {
  opacity: 0.6;
}

.header {
  display: flex;
  text-transform: uppercase;
  font-family: $moderat;
}

.title {
  font-size: 28px;
  line-height: 32px;
  letter-spacing: 1px;
}

.lastSet {
  margin-left: auto;
  font-size: 22px;
  line-height: 28px;
  opacity: 0.8;
}

.content {
  display: flex;
  margin-top: 12px;
}

.info {
  margin-top: 20px;
  width: 176px;
}

.currentHeartRateContainer {
  display: flex;
}

.currentHeartRate {
  font-family: $heroic;
  font-size: 80px;
  line-height: 80px;
  min-width: 82px;
}

.heartRateIcon {
  margin-left: 10px;
  margin-top: 22px;
}

.zone {
  margin-top: 8px;
  font-family: $moderat;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
  opacity: 0.8;
}

.zoneChartWrapper {
  margin-left: 30px;
}

.zoneHighlight5 {
  position: relative;
}

.zoneHighlight4 {
  position: relative;
  top: 33px;
}

.zoneHighlight3 {
  position: relative;
  top: 66px;
}

.zoneHighlight2 {
  position: relative;
  top: 99px;
}

.zoneHighlight1 {
  position: relative;
  top: 132px;
}

.zoneBar {
  position: absolute;
  width: 280px;
  height: 33px;
  opacity: 0.75;
}

.zoneBar5 {
  background: linear-gradient(360deg, #000000 -186.36%, rgba(0, 0, 0, 0) 77.27%), #e15447;
}

.zoneBar4 {
  background: linear-gradient(360deg, #000000 -186.36%, rgba(0, 0, 0, 0) 77.27%), #e38d54;
}

.zoneBar3 {
  background: linear-gradient(360deg, #000000 -186.36%, rgba(0, 0, 0, 0) 77.27%), #a4c373;
}

.zoneBar2 {
  background: linear-gradient(360deg, #000000 -186.36%, rgba(0, 0, 0, 0) 77.27%), #65aab4;
}

.zoneBar1 {
  background: linear-gradient(360deg, #000000 -186.36%, rgba(0, 0, 0, 0) 77.27%), #929090;
}

.zoneHidden {
  opacity: 0;
}

.chart {
  position: absolute;
  width: 264px;
  height: 165px;
  margin: 0px 8px;
}

.zoneNumber {
  width: 24px;
  height: 33px;
  margin-left: 296px;
  text-align: right;
  font-family: $moderat;
  font-weight: 300;
  font-size: 22px;
  line-height: 28px;
  opacity: 0.4;
}
