.container {
  height: 210px;
  width: 228px;
}

.title {
  margin-bottom: 32px;

  font-size: 28px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 1px;
  text-transform: uppercase;

  color: #ffffff;
  opacity: 0.6;
}

.pace {
  margin-bottom: 8px;
  height: 82px;
}

.currentPace {
  font-family: Heroic;
  font-size: 80px;
  font-weight: 400;
  line-height: 80px;

  color: #ffffff;
}

.targetPace {
  font-family: Heroic;
  font-size: 60px;
  font-weight: 400;
  line-height: 72px;

  color: #ffffff;
  opacity: 0.6;
}

.label {
  font-size: 24px;
  font-weight: 400;
  line-height: 28px;
  text-transform: uppercase;

  color: #ffffff;
  opacity: 0.6;
}
