@import 'src/sass/colors';
@import 'src/sass/sizes';

$collection-margin: 48px;
$collection-height: 440px;

.container {
  background-color: $base-dark-grey;
  height: $collection-height;
  width: $screen-width;
  display: flex;
  justify-content: center;
  align-items: center;

  .classes {
    height: 100%;
    width: 100%;
    margin: 0 $collection-margin;

    display: flex;
    justify-content: space-between;
    align-items: center;

    text-align: left;

    .class {
      border-top: 4px solid transparent;
      height: 100%;
      position: relative;
      padding-top: 80px;

      &.selected {
        border-color: $white;
      }

      .actionBar {
        margin-top: 8px;
        height: 76px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
