.button {
  height: 78px;
  width: 78px;
  color: #3d457b;
  border-radius: 50%;
  border: 4px solid #3d457b;
  background-color: white;
  font-size: 40px;
  text-transform: uppercase;
  z-index: 1;

  &:focus {
    outline: none;
  }

  &.active {
    background-color: #3d457b;
    color: white;
  }

  &.disabled {
    opacity: 0.4;
  }
}

.daysContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  margin-bottom: 40px;
  width: 642px;
}
