.track {
  position: relative;
  top: 0;
  left: 0;

  display: block;
  &:before,
  &:after {
    display: grid;

    content: '';
  }
  &:after {
    clear: both;
  }
}

.slide {
  display: block;
  float: left;

  height: 100%;
  min-height: 1px;

  transform: scale(0.8);
  outline: none;

  opacity: 0.3;
  transition: all 300ms ease;
}
.current {
  opacity: 1;
  transform: scale(1);
}
