@import 'src/sass/colors';

.container {
  position: relative;
  width: 620px;
  height: 589px;
  display: flex;
  justify-content: center;
  color: $white;
  overflow: hidden;
  margin-bottom: 50px;

  img {
    height: 528px;
    mask-image: linear-gradient(
        90deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 1) 20%,
        rgba(0, 0, 0, 1) 80%,
        rgba(0, 0, 0, 0) 100%
      ),
      linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 20%);
    mask-composite: intersect;
  }

  .detailsContainer {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 10;

    .badge {
      border-radius: 4px;
      width: 120px;
      padding: 4px 0px;
      text-align: center;
      font-size: 16px;
      line-height: 22px;
      font-weight: 500;
      text-transform: uppercase;
      color: $black;
      background: $white;
      margin-bottom: 18px;
    }

    .title {
      display: flex;
      align-items: center;
      text-align: center;
      font-size: 55px;
      font-weight: 500;
      margin-bottom: 5px;
    }

    .duration {
      font-size: 20px;
      margin-bottom: 64px;
    }

    .startButton {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 50px;
      width: 182px;
      border-radius: 8px;
      border: none;
      font-size: 17px;
      font-weight: 500;
      text-transform: uppercase;
      background-color: $primary;
      color: $white;

      p {
        margin-left: 8px;
      }
    }
  }

  .gradient {
    position: absolute;
    bottom: 65px;
    height: 255px;
    width: 100%;
    background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
  }
}
