@import 'src/sass/colors';

.wrapper {
  border-radius: 10px;
  overflow: scroll;
  max-height: 420px;
  background-color: var(--color-base-30);
  width: 100%;
  text-align: left;
}

.table {
  width: 100%;

  thead tr {
    background-color: var(--color-base-40);
  }

  thead th {
    min-width: 210px;
  }

  tbody tr {
    border-bottom: 1px solid var(--color-base-20);

    &:last-child {
      border-bottom: 0;
    }
  }
}

.fixedWidth {
  width: 450px;
}
