@import 'src/sass/colors';

.wrapper {
  box-shadow: 0 2px 4px 0 #c8d8eb;
  background-color: #ffffff;
  border-radius: 10px;
  margin: 40px 25px 0px 25px;
  padding: 27px;

  .title {
    font-size: 30px;
    text-transform: uppercase;
    color: $dark_blue;
    font-weight: 300;
    border-bottom: 1px solid rgba(67, 76, 91, 0.2);
    padding-bottom: 20px;

    img {
      height: 32px;
      width: 32px;
      margin-bottom: 7px;
      margin-right: 10px;
    }
  }

  .milestones {
    padding: 10px 0;
    display: flex;
    flex-wrap: wrap;
  }
}
