.container {
  width: 192px;
  height: 506px;

  background: linear-gradient(270deg, rgba(0, 0, 0, 0.3) 20.15%, rgba(116, 116, 116, 0.3) 100%);
  backdrop-filter: blur(22.5714px);

  border-radius: 10px 0px 0px 10px;
  overflow: hidden;

  animation: slideLeft 0.5s linear forwards;
}

.header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);

  padding-top: 18px;
  padding-bottom: 22px;
  padding-left: 18px;
  padding-right: 18px;

  font-size: 16px;
  line-height: 19px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: white;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;

  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 18px;
  padding-right: 18px;

  border-bottom: 1px solid rgba(255, 255, 255, 0.6);

  &.moveUp {
    animation: slideUp 0.2s linear forwards;
  }

  &.moveDown {
    animation: slideDown 0.2s linear forwards;
  }
}

.currentUserRow {
  background: rgba(255, 255, 255, 0.2);
}

.avatar {
  margin-right: 12px;

  border-radius: 50%;
  background: white;
  width: 32px;
  height: 32px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
  }
}

.rank {
  margin-right: 4px;
  width: 38px;

  font-size: 16px;
  line-height: 19px;
  font-weight: 300;

  color: white;
}

.verticalDivider {
  margin-top: -12px;
  margin-bottom: -12px;
  margin-right: 8px;
  height: 36px;
  border-right: 1px solid rgba(255, 255, 255, 0.6);
}

.points {
  flex: 1;

  font-weight: normal;
  font-size: 22px;
  line-height: 26px;
  color: white;
  text-align: right;
}

@keyframes slideUp {
  0% {
    opacity: 0.7;
    transform: translate(0px, 64px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes slideDown {
  0% {
    opacity: 0.7;
    transform: translate(0px, -64px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@keyframes slideLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}
