.container {
  position: relative;

  width: 100vw;
  height: 100vh;

  background: black;
}

.video {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ctaContainer {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translateX(-50%);
}

.cta {
  width: 100%;
  border: 2px solid white;
  border-radius: 8px;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 24px;
  padding-right: 24px;

  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  text-transform: uppercase;
  color: white;
  white-space: nowrap;
}
