.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #3d4756;
  padding: 90px 50px 50px;

  .title {
    font-weight: 500;
    font-size: 46px;
    line-height: 46px;
    margin-bottom: 50px;
    text-align: center;
    text-transform: uppercase;
  }
  .body {
    font-size: 36px;
    margin-bottom: 50px;
    text-align: center;
    line-height: normal;
  }
}
