$debug-animation-duration: 60s;
$release-animation-duration: 360s;
$animation-duration: $release-animation-duration;

.wrapper {
  & > .redWeight {
    position: absolute;
    z-index: 0;
    width: 400px;
    animation: redWeightsFall linear forwards;
    animation-duration: $animation-duration * 0.85;
    animation-iteration-count: infinite;
    animation-delay: -$animation-duration * 0.05;
    right: -5px;
    top: -725px;
  }

  & > .lightWeight {
    position: absolute;
    z-index: 0;
    width: 550px;
    right: -80px;
    top: -860px;
    animation: lightWeightsFall linear forwards;
    animation-duration: $animation-duration * 0.65;
    animation-iteration-count: infinite;
    animation-delay: -$animation-duration * 0.23;
  }

  & > .blueWeight {
    position: absolute;
    z-index: 0;
    width: 600px;
    animation: blueWeightFall linear forwards;
    animation-duration: $animation-duration * 0.67;
    animation-iteration-count: infinite;
    animation-delay: -$animation-duration * 0.43;
    left: 30px;
    top: -855px;
  }

  & > .yellowWeight {
    position: absolute;
    z-index: 0;
    width: 500px;
    animation: yellowWeightFall linear forwards;
    animation-duration: $animation-duration * 0.83;
    animation-iteration-count: infinite;
    animation-delay: -$animation-duration * 0.15;
    left: -150px;
    top: -640px;
  }

  @keyframes redWeightsFall {
    from {
      transform: translate3d(0, 0, 0);
    }

    to {
      transform: translate3d(0, calc(100vh + 725px), 0);
    }
  }

  @keyframes lightWeightsFall {
    from {
      transform: translate3d(0, 0, 0);
    }

    to {
      transform: translate3d(0, calc(100vh + 860px), 0);
    }
  }

  @keyframes blueWeightFall {
    from {
      transform: translate3d(0, 0, 0);
    }

    to {
      transform: translate3d(0, calc(100vh + 855px), 0);
    }
  }

  @keyframes yellowWeightFall {
    from {
      transform: translate3d(0, 0, 0);
    }

    to {
      transform: translate3d(0, calc(100vh + 640px), 0);
    }
  }
}
