.container {
  position: relative;
  width: 212px;
  height: 569px;
  background: rgba(0, 0, 0, 0.8);
  overflow: hidden;
  animation: slideLeft 0.5s linear forwards;
  transition: width 300ms, height 300ms;
  border-radius: 13px 0 0 13px;

  &.isExpanded {
    width: 430px;
    height: 692px;
  }

  &.isDismissed {
    width: 64px;
    height: 80px;
  }
}

@keyframes slideLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.dismissed {
  width: 100%;
  height: 100%;
  padding: 20px 0 0 12px;

  svg {
    height: 40px;
    width: 40px;
  }
}
