.modal__container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1500;
  padding-top: 240px;

  .modal__body {
    display: flex;
    border-radius: 10px;
    background-color: var(--color-background-primary);
    width: 858px;
    z-index: 1500;
    flex-direction: column;
    padding: 55px;
    justify-content: space-between;
    align-items: center;

    .buttons {
      display: flex;
      flex-direction: row-reverse;

      .close {
        margin-left: 20px;
      }
    }
  }
}
