@import 'src/sass/fonts.scss';
@import 'src/sass/colors.scss';

.title {
  font-weight: 500;
  font-size: 26px;
  line-height: 31px;
  color: $dark_blue;
  white-space: nowrap;
}

.classList {
  margin: 24px 0 0 0;
  padding: 0;
  color: $dark_blue;

  li {
    display: flex;
    padding-bottom: 16px;

    .details {
      width: 330px;
      margin-right: 20px;
      font-size: 22px;
      line-height: 28px;

      .classTitle {
        font-weight: 500;
      }

      .instructorName {
        font-weight: normal;
      }
    }
  }

  li:not(:last-child) {
    margin-bottom: 16px;
    border-bottom: 1px solid rgba($color: $dark_blue, $alpha: 0.33);
  }
}
