@import 'src/sass/colors';

.header {
  box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.35);
  color: var(--color-base-70);
}

.progressBar {
  height: 8px;
  background-color: var(--color-base-70);
  border-radius: 10px;
  margin-bottom: 8px;

  .progress {
    height: 100%;
    background-color: var(--color-base-20);
    border-radius: 10px;
  }
}
