@import '../../sass/colors';

.modalContainer {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  z-index: 1500;

  .modalBg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0.5;
    background-color: black;
    z-index: 1400;
  }

  .modal {
    border-radius: 10px;
    background-image: linear-gradient(149deg, #f6fafe 7%, #d9e3f0 95%);
    margin: auto;
    width: 85vw;
    z-index: 1500;
    padding: 30px;
    max-height: 100vh;

    .buttons {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;

      .close {
        margin-left: 20px;
        width: 23px;
        height: 23px;
      }
    }

    .body {
      height: 100%;
    }
  }
}
