@import 'src/sass/fonts';

.container {
  margin: 50px 20px 30px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  min-height: 500px;
  text-align: center;

  .header {
    margin-bottom: 60px;

    h1 {
      margin: 0px;
    }
  }

  h1 {
    font-family: $moderat;
    font-size: 45px;
    font-weight: 500;
    line-height: 1.36;
    color: #3d4756;
    margin-bottom: 10px;
  }

  .steps {
    list-style: none;
    counter-reset: custom-counter;
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    display: flex;
    justify-content: space-evenly;

    li {
      counter-increment: custom-counter;
      margin-bottom: 20px;
      margin-right: 0px;
      font-size: 24px;
      font-weight: 200;
      color: #434c5b;
      text-align: left;

      &::before {
        content: counter(custom-counter);
        font-family: $heroic;
        font-stretch: condensed;
        font-size: 86px;
        display: block;
        font-weight: 400;
        line-height: 104px;
      }
    }
    li:not(:last-child) {
      margin-right: 20px;
    }
  }
}

.back {
  margin-right: auto;
  padding: 0;
}
