@import 'src/sass/fonts';

.container {
  margin: 50px 20px 30px;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  min-height: 500px;
  text-align: center;

  h1 {
    font-family: $moderat;
    font-size: 45px;
    font-weight: 500;
    line-height: 1.36;
    color: #3d4756;
    margin: 30px 0 10px;
  }
}

.back {
  margin-right: auto;
  padding: 0;
}
