@import 'src/sass/colors';

.wrapper {
  border-bottom: 2px solid rgba(67, 76, 91, 0.4);
  box-shadow: 0px 7px 13px rgba(61, 69, 123, 0.35);

  min-height: 104px;

  &.sticky {
    position: sticky;
    top: 0;
    z-index: 10;
  }
}

.navLink {
  display: flex;
  width: 100%;
  color: rgba(67, 76, 91, 0.4);

  width: 204px;
  height: 54px;
  margin-right: 32px;
  font-size: 22px;
  font-weight: 500;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;

  z-index: 10;

  &.active {
    text-decoration: none;
    color: $base-blue;
    margin-bottom: -2px;
    border-bottom: solid 4px $base-blue;
  }
}
