@import 'src/sass/colors';

.container {
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;

  .lines {
    text-align: center;
    margin-top: 675px;

    p {
      margin: 0px;
      font-size: 64px;
      line-height: 70px;
      color: $white;
      animation: fadeIn linear 1s;
    }

    &.inPosition {
      margin-top: 120px;
    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
