.animated {
  transition: all 500ms ease-in-out;
}

.meter {
  position: relative;
  width: 416px;
  height: 68px;
  backdrop-filter: blur(60px);
  border: 4px solid var(--color-base-30);
  border-radius: 13px;
  overflow: hidden;
}

.targetZone {
  position: absolute;
  top: 5px;
  bottom: 5px;
  border-radius: 8px;
  background: var(--color-base-40);
}

.marker {
  position: absolute;
  width: 10px;
  top: 0;
  bottom: 0;
  transition: left 50ms linear, background-color 50ms linear;
}

.gradient {
  position: absolute;
  opacity: 0.5;
  min-height: 48px;
  top: 6px;
}

.normal {
  background: var(--color-white);
}

.good {
  // child component on top blends between this color and the target color
  background: var(--color-white);
}

.target {
  background: var(--color-green);
}
