.animated {
  transition: all 500ms ease-in-out;
}

.meter {
  position: relative;
  width: 416px;
  height: 68px;
  backdrop-filter: blur(60px);
  border: 4px solid var(--color-base-30);
  border-radius: 13px;
  overflow: hidden;
}

.targetZone {
  position: absolute;
  top: 5px;
  bottom: 5px;
  border-radius: 8px;
  background: var(--color-base-40);
}

.marker {
  position: absolute;
  width: 10px;
  top: 0;
  bottom: 0;
  z-index: 2;
  transition: left 50ms linear, background-color 50ms linear;
}

.smallMarker {
  border-radius: 8px;
  top: 6px;
  bottom: 6px;
  width: 8px;
}

.normal {
  background: var(--color-white);
}

.lastRep {
  background: var(--color-base-40);
  width: 10px;
  height: 48px;
  border-radius: 4px;
  top: 6px;
  transition: none;
}

.target {
  background: var(--color-green);
  box-shadow: 0px 0px 201px rgba(126, 201, 127, 0.98), -11.78px 0px 92.9284px rgba(126, 201, 127, 0.726842),
    -0.84px 0px 19.4469px rgba(126, 201, 127, 0.450851), 1.97px 0px 10.8293px rgba(126, 201, 127, 0.365755),
    0.4px 0px 4.65761px rgba(126, 201, 127, 0.253158);
}

$trail-spread: 10px;
$trail-blur: 50px;

// Gives enough room for the shadow to decay without creating a hard edge
$trail-inset: -$trail-blur * 1.5;

.trail-normal {
  box-shadow: 0px 0px $trail-blur $trail-spread var(--color-white);
}

.trail-target {
  box-shadow: 0px 0px $trail-blur $trail-spread var(--color-green);
}

.trail-left {
  // Helps crop the shadow to left/right side WITHOUT dimming it.
  clip-path: inset(0px 0px 0px $trail-inset);
}

.trail-right {
  // Helps crop the shadow to left/right side WITHOUT dimming it.
  clip-path: inset(0px $trail-inset 0px 0px);
}
