.container {
  position: relative;
  background-color: black;

  .image {
    width: 100%;
  }

  .gradient {
    height: 22.5rem;
    margin-top: -22.5rem;
    position: relative;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), black);
  }

  .info {
    position: absolute;
    bottom: 3rem;
    left: 3rem;
    right: 3rem;
  }
}
