@import 'src/sass/fonts';
@import 'src/sass/colors';

.page {
  display: flex;
  flex-direction: column;
  min-height: 805px;
  width: 100%;

  .page__nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    min-height: 63px;

    > *:first-child {
      margin-right: auto;
    }

    > *:last-child {
      margin-right: 0;
      margin-left: 1rem;
    }

    .backBtn {
      padding: 0;
    }
  }

  .page__contents {
    margin-top: 70px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    h1 {
      font-family: $moderat;
      font-size: 32px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: 1;
      letter-spacing: normal;
      color: #434c5b;
      margin-bottom: 45px;
      margin-top: 0px;
    }

    h2 {
      font-family: $moderat;
      font-size: 35px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.43;
      letter-spacing: normal;
      color: #434c5b;
      text-align: center;
      padding: 0 55px;
      margin-bottom: 60px;
    }

    h3 {
      font-family: $moderat;
      font-size: 20px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #434c5b;
      text-transform: uppercase;
    }
  }
}
