@import 'src/sass/colors';
@import 'src/sass/fonts';

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $dark_blue;

  h1 {
    font-family: Moderat;
    font-style: normal;
    font-weight: 500;
    font-size: 52px;
    line-height: 64px;
    text-align: center;
    color: $dark_blue;
    margin: 0;
  }

  .message {
    @include body-l-regular;
    text-align: center;
    margin-top: 32px;
  }

  .progress {
    width: 54%;
    height: 6px;
    margin: 96px 75px 0 75px;
    background-color: $pivot-light-blue;
    border-radius: 0;
  }

  .progressBar {
    background-color: #2e3e80;
  }

  .icon {
    width: 60px;
    height: 60px;
  }

  .counterMessage {
    @include body-uppercase;
    text-align: center;
    margin-top: 64px;
  }

  .counter {
    display: inline-block;
    width: 18px;
  }
}

.progressStatus h1 {
  margin-top: 92px;
}

.doneStatus h1 {
  margin-top: 32px;
}

.failedStatus h1 {
  margin-top: 28px;
}
