@import 'src/sass/colors';

.infoPanel {
  box-shadow: 0 7px 13px 0 rgba(61, 69, 123, 0.35);
  background-color: linear-gradient(115deg, #f6fafe, #d9e3f0);
  z-index: 1;
  color: $brand-blue;
  background-color: $white;

  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 30px;
  }

  .info {
    display: flex;

    > div {
      flex: 1;
    }

    > div:first-child {
      padding: 23px 32px 50px;
    }
  }

  .tags {
    div + div {
      margin-left: 36px;
    }
  }

  .pausedChip {
    font-size: 16px;
    font-weight: 500;
    text-transform: uppercase;
    background-color: #c8d8eb;
    width: fit-content;
    padding: 4px 16px;
    border-radius: 10px;
    visibility: hidden;

    &.visible {
      visibility: visible;
    }
  }

  .instructor {
    margin-top: 24px;
    font-size: 22px;
    font-weight: 500;
    text-transform: uppercase;
  }

  .title {
    margin-top: 8px;
    margin-bottom: 24px;
    font-size: 52px;
    font-weight: 700;
    line-height: 64px;
    text-transform: uppercase;
  }

  .description {
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 24px;
  }

  .attributes {
    display: flex;

    > span {
      font-size: 16px;
      font-weight: 500;
      line-height: 28px;

      &:not(:first-child) {
        padding-left: 32px;
      }

      &:not(:last-child) {
        padding-right: 32px;
        border-right: 2px solid #3d457b;
      }

      div {
        font-size: 26px;
        font-weight: 500;
      }
    }
  }

  .thumbnail {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
  }
}
